<template>
  <section>
    <el-col :span="24" style="padding:10px;">
      <el-form :model="form" label-width="120px" @submit.prevent="onSubmit" size="mini" style="text-align:left;padding:10px;width:30%;min-width:200px;">
        <el-form-item label="漏洞名称名称">
          <el-input v-model="form.vul_name" placeholder="查询分组名称" size="small"></el-input>
        </el-form-item>
        <el-form-item label="运营字段">
          <el-input v-model="form.operator" placeholder="查询运营字段" size="small"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" size="small">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-col :span="24" style="padding:0px 10px 10px 40px;">
      <el-table :data="tableData" stripe style="width:100%" border size="small" highlight-current-row>
        <el-table-column label="序号">
          <template slot-scope="scope">
            {{scope.$index+1}}
          </template>
        </el-table-column>
        <el-table-column prop="vul_name" label="漏洞名称">
        </el-table-column>
        <el-table-column prop="username" label="提交人">
        </el-table-column>
        <el-table-column prop="open_field" label="运营字段">
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="toverify(scope.row)">审核</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="24" style="padding:20px 10px 10px 40px;text-align:left;">
      <el-pagination layout="total, prev, pager, next ,sizes ,jumper" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-size="20" :page-sizes="[10,20, 50, 100]" :total="total" style="float:left;">
      </el-pagination>
    </el-col>
  </section>
</template>
<script>
import { getauditinfos } from '../../api/api';
export default {
  data() {
    return {
      form: {
        vul_name: '',
        operator: ''
      },
      tableData: [],
      total:0,
    }
  },
  method: {
    search: function() {
      let params={
        audit_status: 0,
        vul_name: this.form.vul_name,
        open_field: this.form.operator,
      };
      getauditinfos(params).then(res => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.tableData = result.result;
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    toverify(obj) {
      this.$router.push({
        path: '/vuladuit',
        name: 'vuladuit',
        params: {
          aduit_info: obj
        }
      });
      let params = {
      }
    }
  },
  created(){
    let params = {audit_status: 0};
    getauditinfos(params).then(res => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.tableData = result.result;
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
  }
}

</script>



