<template>
  <section>
    <el-col :span="24" style="padding:10px;">
      <el-form :inline="true" :model="form" class="demo-form-inline" style="text-align:left;padding:10px;margin-left:20px;">
        <el-form-item label="分组名称">
          <el-input v-model="form.name" placeholder="查询分组名称" size="small" @keyup.enter.native="search"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" size="small">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-col :span="24" style="padding:0px 10px 10px 40px;text-align:left;">
      <el-button type="primary" @click="toaddgp(),dialogFormVisible = true" size="small">添加组</el-button>
    </el-col>
    <el-dialog :title="dialogtitle" :visible.sync="dialogFormVisible" width="30%">
      <el-form :model="addgp" size="mini">
        <el-form-item label="组名称" :label-width="formLabelWidth">
          <el-input v-model="addgp.name" placeholder="填写组名称"></el-input>
        </el-form-item>
        <el-form-item label="备注" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="addgp.desc" placeholder="请填写备注">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer" v-if="this.dialogtitle=='新建账号组'">
        <el-button type="primary" size="small" @click="submitForm('addgp','add'),dialogFormVisible = false">确 定</el-button>
        <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
      </div>
      <div slot="footer" class="dialog-footer" v-if="this.dialogtitle=='编辑账号组'">
        <el-button type="primary" size="small" @click="submitForm('addgp','edit',addgp.group_uuid),dialogFormVisible = false">确 定</el-button>
        <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
      </div>
    </el-dialog>
    <el-dialog :title="dialogtitle" :visible.sync="dialogFormVisible1" width="50%">
      <el-transfer v-model="value" :data="transferdata" style="text-align: left; display: inline-block" :titles="['可选权限  ', '选中权限']">
      </el-transfer>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="small" @click="dialogFormVisible1 = false,tosavepremission()">确 定</el-button>
        <el-button @click="dialogFormVisible1 = false" size="small">取 消</el-button>
      </div>
    </el-dialog>
    <el-col :span="24" style="padding:0px 10px 10px 40px;">
      <el-table :data="tableData" stripe style="width:100%" border stripe size="small" highlight-current-row>
        <el-table-column prop="name" label="名称">
        </el-table-column>
        <el-table-column prop="desc" label="备注">
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间">
        </el-table-column>
        <el-table-column prop="creator" label="创建者">
        </el-table-column>
        <el-table-column label="操作" width="250">
          <template slot-scope="scope">
            <el-link :underline="false" @click="toaccountlist(scope.row.name)">账号列表</el-link>
            <el-link :underline="false" @click="editgroup(scope.row),dialogFormVisible = true">编辑组</el-link>
            <el-link :underline="false" @click="togetpremission(scope.row.group_uuid),dialogFormVisible1=true">编辑权限</el-link>
            <el-link :underline="false" @click="todeletegroup(scope.row.group_uuid)">删除</el-link>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
  </section>
</template>
<script>
import MiddleWare from '../../common/js/middleware'
import { getaccoutgroup, addgroup, deletegroup, getpremissions, savepremission } from '../../api/api'
export default {
  data() {
    return {
      form: {
        name: '',
      },
      transferdata: [],
      value: [],
      operator: true,
      tableData: [],
      dialogtitle: '',
      dialogFormVisible: false,
      dialogFormVisible1: false,
      addgp: {
        name: '',
        desc: '',
      },
      formLabelWidth: '60px'
    }
  },
  methods: {
    getGrouplist(name) {
      if (name == undefined) {
        let params = {}
        getaccoutgroup(params).then((res) => {
          let { status, message, result } = res.data;
          if (status == 2000) {
            this.tableData = result.result
          } else {
            console.log('error')
          }
        });
      } else {
        let params = {}
        params.name = name
        getaccoutgroup(params).then((res) => {
          let { status, message, result } = res.data;
          if (status == 2000) {
            this.tableData = result.result
          } else {
            console.log('error')
          }
        });
      }
    },
    search: function() {
      let name = this.form.name
      this.getGrouplist(name);
    },
    submitForm(formName, action, group_uuid) {
      let params = {
        name: this.addgp.name,
        desc: this.addgp.desc
      };
      params.action = action
      params.group_uuid = group_uuid
      addgroup(params).then(data => {
        let { status, message } = data;
        if (status == 2000) {
          this.getGrouplist();
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    toaddgp() {
      this.dialogtitle = '新建账号组'
      this.addgp = {
        name: '',
        desc: '',
      }
    },
    toaccountlist(name) {
      this.$emit('postgroup')
      MiddleWare.$emit('postgroup', name)
    },
    editgroup(obj) {
      this.dialogtitle = '编辑账号组'
      this.addgp = obj
    },
    togetpremission(id) {
      this.group_uuid = id
      let params = {
        group_uuid: id
      }
      getpremissions(params).then((res) => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.transferdata = []
          for (let i of result.all_perms) {
            let perm = {}
            perm.label = i.name
            perm.key = i.id
            this.transferdata.push(perm)
          }
          this.value = []
          for (let i of result.selected_perms) {
            let key = i.id
            this.value.push(key)
          }
        } else {
          console.log('default')
        }
      })
    },
    tosavepremission() {
      let params = {
        group_uuid: this.group_uuid,
        selected_permids: this.value.join(',')
      }
      savepremission(params).then(data => {
        let { status, message } = data;
        if (status == 2000) {
          console.log('success')
        } else {
          console.log('default')
        }
      })
    },
    todeletegroup(id) {
      let params = {
        group_uuid: id
      }
      deletegroup(params).then(data => {
        let { message, status } = data;
        if (status == 2000) {
          this.getGrouplist();
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    }
  },
  created() {
    this.getGrouplist();
  },
}

</script>
<style scoped>
.el-table {
  color: #333;
  font-size: 14px;
}

.el-table>>>.cell {
  text-align: center;
}

</style>



