<template>
  <section>
    <el-col :span="10" style="margin-left:80px;padding:10px;border:1px dashed #C0C4CC">
      <el-form label-position="left" inline class="demo-table-expand" size="medium">
        <el-form-item label="登录名" label-width="80px">
          <span>{{sysUserName}}</span>
        </el-form-item>
        <el-form-item label="姓名" label-width="80px">
          <span>{{sysName}}</span>
        </el-form-item>
        <el-form-item label="手机号码" label-width="80px">
          <span>{{sysPhonenum}}</span>
        </el-form-item>
        <el-form-item label="邮箱" label-width="80px">
          <span>{{sysEmail}}</span>
        </el-form-item>
        <el-form-item label="部门" label-width="80px">
          <span>{{sysDepartment}}</span>
        </el-form-item>
        <el-form-item label="职务" label-width="80px">
          <span>{{sysPosition}}</span>
        </el-form-item>
      </el-form>
    </el-col>
  </section>
</template>
<script>
import { getpersonalinfo } from '../../api/api';
export default {
  data() {
    return {
      sysUserName: '',
      sysName:'',
      sysPhonenum:'',
      sysEmail:'',
      sysDepartment:'',
      sysPosition:'',
    }
  },
  created() {
    getpersonalinfo().then((res) => {
      let { status, message, result } = res.data;
      if (status == 2000) {
        this.sysUserName = result.username,
        this.sysName = result.chinese_name,
        this.sysPhonenum = result.phone,
        this.sysEmail= result.email,
        this.sysDepartment= result.department,
        this.sysPosition= result.position
      } else {
        console.log('default')
      }
    });
  }
}

</script>
<style>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

</style>



