<template>
  <section>
    <el-form ref="form" :model="form" label-width="150px" @submit.prevent="onSubmit" style="margin:20px;width:50%;min-width:400px;text-align:left;" size="mini">
      <el-form-item label="漏洞名称">
        <el-input v-model="form.vul_name"></el-input>
      </el-form-item>
      <el-form-item label="漏洞ID">
        <el-input v-model="form.vul_id"></el-input>
      </el-form-item>
      <el-form-item label="发现时间">
        <el-col :span="9">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.find_date" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2" style="text-align:center;font-size:12px;"> 至</el-col>
        <el-col :span="10">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.find_date" style="width: 100%;"></el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="危险等级">
        <el-checkbox-group v-model="form.severity">
          <el-checkbox v-for="item in severity" :label="item.value" :key="item.id">{{item.name}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="CVE编号">
        <el-input v-model="form.cve_id"></el-input>
      </el-form-item>
      <el-form-item label="是否有修复建议">
        <el-checkbox-group v-model="form.solution">
          <el-checkbox v-for="item in solution" :label="item.value" :key="item.id">{{item.name}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <div class="collapsebtn">
        <i v-if="show==true" class="el-icon-arrow-up" style="text-indent:10px;"></i>
        <i v-else class="el-icon-arrow-down" style="text-indent:10px;"></i>
        <el-link @click="show = !show" size="small" :underline="false">高级搜索</el-link>
      </div>
      <el-collapse-transition>
        <div v-show="show">
          <el-form-item label="扫描类型">
            <el-checkbox-group v-model="form.scan_type">
              <el-checkbox v-for="item in scan_type" :label="item.value" :key="item.id">{{item.name}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="应用名称">
            <el-input v-model="form.pg_style2"></el-input>
          </el-form-item>
          <el-form-item label="服务名称">
            <el-input v-model="form.pg_style1"></el-input>
          </el-form-item>
          <el-form-item label="操作系统">
            <el-input v-model="form.pg_style3"></el-input>
          </el-form-item>
          <el-form-item label="WAF是否可以防护扫描">
            <el-checkbox-group v-model="form.defend_scan">
              <el-checkbox v-for="item in defend_scan" :label="item.value" :key="item.id">{{item.name}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="WAF是否可以防护漏洞">
            <el-checkbox-group v-model="form.defend_vul">
              <el-checkbox v-for="item in defend_vul" :label="item.value" :key="item.id">{{item.name}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="是否后续支持">
            <el-checkbox-group v-model="form.continue_support">
              <el-checkbox v-for="item in continue_support" :label="item.value" :key="item.id">{{item.name}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="WAF防护信息">
            <el-popover ref="popover" placement="bottom" title="WAF防护信息树型结构" width="400" trigger="focus" content="">
              <el-tree class="filter-tree" :data="treedata" :props="defaultProps" show-checkbox node-key="id" ref="tree">
              </el-tree>
              <el-button @click="treeclick(),treeinfo=!treeinfo">选择完成</el-button>
            </el-popover>
            <el-input :placeholder="form.rule_type.join(',')" v-popover:popover></el-input>
          </el-form-item>
          <el-form-item label="WAF规则id">
            <el-input v-model="form.rule_id"></el-input>
          </el-form-item>
          <el-form-item label="可利用程度">
            <el-checkbox-group v-model="form.use_level">
              <el-checkbox v-for="item in use_level" :label="item.value" :key="item.id">{{item.name}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-row :gutter="24">
            <el-col :span="12">
              <el-form-item label="金融">
                <el-select v-model="form.finance">
                  <el-option v-for="item in reinforce_level" :label="item.name" :value="item.value" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="运营商">
                <el-select v-model="form.operator">
                  <el-option v-for="item in reinforce_level" :label="item.name" :value="item.value" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="烟草">
                <el-select v-model="form.tobacco">
                  <el-option v-for="item in reinforce_level" :label="item.name" :value="item.value" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="能源">
                <el-select v-model="form.energy">
                  <el-option v-for="item in reinforce_level" :label="item.name" :value="item.value" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="政府">
                <el-select v-model="form.government">
                  <el-option v-for="item in reinforce_level" :label="item.name" :value="item.value" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="通用">
                <el-select v-model="form.common">
                  <el-option v-for="item in reinforce_level" :label="item.name" :value="item.value" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </el-collapse-transition>
      <el-form-item style="margin-top:20px;">
        <el-button type="primary" size="small" @click="search" icon="el-icon-search">查询</el-button>
      </el-form-item>
    </el-form>
  </section>
</template>
<script>
import { forminit } from '../../api/api'
import MiddleWare from '../../common/js/middleware'
export default {
  data() {
    return {
      select: true,
      operator: true,
      form: {
        vul_name: '',
        vul_id: '',
        find_date: '',
        severity: [],
        cve_id: '',
        solution: [],
        scan_type: [],
        pg_style2: '',
        pg_style1: '',
        pg_style3: '',
        defend_scan: [],
        defend_vul: [],
        continue_support: [],
        app_name: '',
        rule_id: '',
        use_level: [],
        finance: [],
        operator: [],
        tobacco: [],
        energy: [],
        government: [],
        common: [],
        rule_type: [],
      },
      params: {},
      severity: [],
      treedata: [],
      solution: [
        { id: 1, name: '是', value: 1 },
        { id: 2, name: '否', value: 2 }
      ],
      scan_type: [],
      defend_scan: [],
      defend_vul: [],
      continue_support: [],
      use_level: [],
      reinforce_level: [],
      show: false,
      treeinfo: false,
    }
  },
  methods: {
    treeclick: function() {
      this.form.rule_type = []
      this.$refs.tree.getCheckedNodes().forEach(item => {
        this.form.rule_type.push(item.label)
      })
      this.form.rule_type.join(',')
    },
    search: function() {
      this.params={};
      for (let i in this.form) {
        if (this.form[i] != '') {
          if (this.form[i].constructor == Array) {
            this.params[i] = this.form[i].join()
          } else {
            this.params[i] = this.form[i]
          }
        }
      }
      MiddleWare.$emit('datachild', this.params)
    },
  },
  created() {
    forminit().then((res) => {
      let { status, message, result } = res.data;
      if (status == 2000) {
        this.scan_type = result.objects.vul.scan_type;
        this.severity = result.objects.vul.severity;
        this.defend_scan = result.objects.soc_waf.defend_scan;
        this.defend_vul = result.objects.soc_waf.defend_vul;
        this.continue_support = result.objects.soc_waf.continue_support;
        this.use_level = result.objects.service.use_level;
        this.reinforce_level = result.objects.service.reinforce_level;
        result.objects.waf_defend.forEach(item => {
          if (item.pname == undefined && item.pId == 0) {
            let obj1 = {}
            obj1.id = item.id
            obj1.label = item.name
            obj1.children = []
            this.treedata.push(obj1)
            result.objects.waf_defend.forEach(item => {
              if (item.pname == undefined && item.pId == obj1.id) {
                let obj2 = {}
                obj2.id = item.id
                obj2.label = item.name
                obj2.children = []
                obj1.children.push(obj2)
                result.objects.waf_defend.forEach(item => {
                  if (item.pname != undefined && item.pname == obj2.label) {
                    let obj3 = {}
                    obj3.id = item.id
                    obj3.label = item.name
                    obj2.children.push(obj3)
                  }
                })
              }
            })
          }
        });
      }
    });
  }
}

</script>
<style scoped>
.el-checkbox {
  margin-right: 5px;
}

.el-form>>>.el-form-item__label {
  font-size: 12px;
}

.el-checkbox>>>.el-checkbox__label {
  font-size: 12px;
}

.el-form-item.el-form-item--mini {
  margin-bottom: 10px;
}

</style>



