<template>
  <el-container>
    <el-main>
      <div id="background1"></div>
      <div id="background2"></div>
      <div id="background3"></div>
      <div id="background4"></div>
      <el-row type="flex" class="row-bg" justify="center">
        <el-col class="signin-box">
          <div class="left-box">
            <h1>智能楼宇管理系统</h1>
            <p>新结构CPBS智能楼宇管理系统 新结构CPBS智能楼宇管理系统 新结构CPBS智能楼宇管理系统 新结构CPBS智能楼宇管理系统 新结构CPBS智能楼宇管理系统 新结构CPBS智能楼宇管理系统</p>
          </div>
          <div class="right-box">
            <div class="grid-content bg-purple-light"><br>
              <h1>登录</h1>
              <p>欢迎回来，登录以继续操作</p>
              <br />
              <br />
              <br>
              <br>
              <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
                <el-form-item prop="name">
                  <el-input v-model="ruleForm.name" placeholder="用户名" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item prop="password">
                  <el-input type="password" v-model="ruleForm.password" placeholder="密码PIN+令牌码" auto-complete="off" @keyup.enter.native="submitForm('ruleForm')"></el-input>
                </el-form-item>
                <br>
                <br>
                <el-form-item>
                  <el-button type="primary" @click="submitForm('ruleForm')" id="loginbtn">登录</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>
<script>
import { getaccountperms } from '../../api/api';
import { asyncRouterMap } from '../../router/index';
import headertop from '../../components/common/header';
import { requsetLogin } from '../../api/api';
export default {
  components: {
    headertop
  },
  data() {
    return {
      ruleForm: {
        logging: false,
        name: '',
        password: '',
        checked: true,
      },
      rules: {
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.logging = true;
          var loginParams = {
            login_name: this.ruleForm.name,
            password: this.ruleForm.password
          };
          requsetLogin(loginParams).then(data => {
            this.logging = false;
            let { status, message } = data;
            if (status != 2000) {
              this.$message({
                showClose: true,
                message: message,
                type: 'warning'
              });
            } else {
              //开始权限请求
              getaccountperms().then(res => {
                let { status, message, result } = res.data;
                if (status == 2000) {
                  result.forEach((item) => {
                    asyncRouterMap.find((s) => {
                      if (s.children) {
                        if (s.path==='/'&&s.children[0].path === item.path) {
                          s.children[0].meta.premission = item.premission
                          this.$router.options.routes.push(s)
                          this.$router.addRoutes(this.$router.options.routes)
                        }
                      } else {
                        if (s.path === item.path) {
                          s.meta.premission = item.premission
                          this.$router.options.routes.push(s)
                          this.$router.addRoutes(this.$router.options.routes)
                        }
                      }
                    })
                  })
                }
              })
              this.$message({
                showClose: true,
                message: '登录成功',
                type: 'success'
              });
              sessionStorage.setItem('user', JSON.stringify(loginParams.login_name));
              this.$router.push('/home');
            }
          });
        } else {
          return false;
        }
      })
    },
  }
}

</script>
<style scoped>
.el-container {
  height: 100%;
  width: 100%;
}

.el-main {
  background: rgba(7, 54, 76, 1);
  padding: 0;
  margin: 0;
  overflow: hidden;
}

#background1 {
  width: 1200px;
  height: 1200px;
  background: #20919D;
  border-radius: 50%;
  position: fixed;
  top: -200px;
  left: -500px;
}

#background2 {
  width: 500px;
  height: 500px;
  background: #20919D;
  border-radius: 50%;
  position: fixed;
  top: 600px;
  right: -300px;
}

#background3 {
  width: 142px;
  height: 142px;
  background: #20919D;
  border-radius: 50%;
  position: fixed;
  top: 50px;
  right: 100px;
  z-index: 1000;
  box-shadow: 0 0px 0px rgba(7, 39, 43, 0.5), 0 0 4px rgba(7, 39, 43, 0.5);
}

#background4 {
  width: 60px;
  height: 60px;
  background: #3FB3C0;
  border-radius: 50%;
  position: fixed;
  top: 150px;
  right: 100px;
  z-index: 1000;
  box-shadow: 0 0px 0px rgba(7, 39, 43, 0.5), 0 0 0px rgba(7, 39, 43, 0.5)
}

.signin-box {
  background: rgba(255, 255, 255, 1);
  width: 950px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  height: 600px;
  text-align: left;
  margin: 100px 0px;
  z-index: 999;
  overflow: hidden;
  border-radius: 6px;
}

.left-box {
  width: 30%;
  float: left;
  height: 100%;
  background: url(../../assets/img/bg/login-bg.png) no-repeat;
  text-align: center;
}

.left-box h1 {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
  line-height: 60px;
}

.left-box h1 img {
  position: relative;
  top: 10px;
  padding-right: 10px;
}

.left-box p {
  color: #d2d2d2;
  font-size: 14px;
  padding: 0px 40px;
  text-align: left;
  position: relative;
  top: 20%;
  line-height: 25px;
}

.right-box {
  float: left;
  height: 100%;
  width: 70%;
  text-align: center;
}

.right-box h1 {
  font-size: 30px;
  font-weight: 100;
}

.right-box p {
  color: rgba(32, 145, 157, 1);
  font-size: 16px;
  margin: 10px;
}

.demo-ruleForm {
  padding: 0px 150px 0px 150px;
  min-width: 220px;
}

#loginbtn {
  margin-top: 20px;
  width: 340px;
  background: rgba(32, 145, 157, 1);
  border-radius: 29.6px;
  height: 48px;
  box-shadow: 0 0px 4px rgba(0, 0, 0, .12), 0 0 4px rgba(0, 0, 0, .04);
}

</style>



