<template>
  <section>
  <el-col :span="24" style="padding:0px 10px 10px 40px;">
      <el-table :data="tableData" stripe style="width:100%" border size="small" highlight-current-row>
        <el-table-column prop="number" label="序号" width="160">
        </el-table-column>
        <el-table-column prop="term" label="消费项目">
        </el-table-column>
        <el-table-column prop="points" label="消费分值" width="280">
        </el-table-column>
        <el-table-column prop="date" label="消费日期" width="280">
        </el-table-column>
      </el-table>
  </el-col>
 <el-col :span="24" style="padding:20px 10px 10px 40px;text-align:left;">
      <el-pagination layout="total, prev, pager, next ,sizes ,jumper" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-size="20" :page-sizes="[10,20, 50, 100]" :total="total" style="float:left;">
      </el-pagination>
  </el-col>
  </section>
</template>
<script>
export default {
 data() {
    return {
	      tableData:[]
    } 
 }
}

</script>
<style scoped>
.el-table {
  color: #333;
  font-size: 14px;
}

.el-table>>>.cell {
  text-align: center;
}

</style>



