<template>
  <table class="mailTable" :style="styleObject" v-if="s_showByRow">
    <tr v-for="index in rowCount">
      <td class="column">{{tableKey[index-1]}}</td>
      <td>{{tableData[index-1]}}</td>
<!--      <td class="column">{{tableData[index*2-1] !== undefined ? tableData[index*2-1].key : ''}}</td>-->
<!--      <td>{{tableData[index*2-1] !== undefined ? tableData[index*2-1].value : ''}}</td>-->
    </tr>
  </table>
</template>
<script>
  export default {
    data() {
      return {
        tableKey:["人数/个","人均面积/(个/m2)","房间职能","温度/℃","湿度/%","PM2.5/(μg/m3)","CO2/ppm"],
        styleObject: {},
        s_showByRow: true,
      };
    },
    props: ['tableData', 'tableStyle', 'showByRow'],
    computed: {
      rowCount: function() {
        // return this.tableDate.length;
        return Math.ceil(this.tableKey.length/1);
      }
    },
    created() {
      this.styleObject = this.tableStyle;
      if(this.showByRow !== undefined){
        this.s_showByRow = this.showByRow;
      }
    },
  }
</script>




