<template>
  <section>
    <el-col :span="24" class="toolbar" style="padding:20px 80px 20px 80px">
       <!-- <el-col :span="2" style="margin-top:10px;">
       <img :src="this.sysUserAvatar" class="avatar">
      </el-col> -->
      <el-col :span="24" style="font-size:0;margin-top:10px;">
         <div style="white-space:nowrap;text-align:left;margin-left:55px;">
            <textarea placeholder="请输入内容" v-model="comment">
            </textarea>
            <el-button type="primary" @click="submitComment">提交评论</el-button>
         </div>
         </el-col>
    <el-col style="text-align:left;margin-left:51px;">
      <el-checkbox v-model="anonym">匿名回复</el-checkbox>
    </el-col>
  </el-col> 
  
  </section>
</template>
<script>
import {addcomment} from '../../api/api';
export default {
  data(){
    return{
      comment:'',
      anonym: false,
    }
  },
 methods: {
  submitComment() {
      let params = {
        vul_id:this.$route.query.id,
        //userAvatar:this.sysUserAvatar,
        content:this.comment,
        is_anonymous:this.anonym,
        comment_id:0,
        //user_id: 'wang'
      };
       addcomment(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.comment = '',
          //location.reload();
          this.$message({
            showClose: true,
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      });
    }
  },
}
</script>
<style scoped>
img {
  display:inline-block;
  vertical-align:top;
  left:140px;
  position:absolute;
  width:60px;
  height:60px;
  padding: 0;
  margin-left:0px;
  border: 0;
}
textarea {
  display:inline-block;
  vertical-align:top;
  margin-bottom:10px;
  margin-right:0px;
  width:79%;
  height:100px;
  border-radius:0px;
  resize: none;
}

/*button {
  display:inline-block;
  vertical-align:top;
  margin-bottom:10px;
  margin-left:0px;
  width:12%;
  height:106px;
  border-radius:0px;
  border:0px;
  background-color:#009999;
  color:#FFFFFF;
}*/
</style>


