<template>
  <section>
    <el-col :span="24" style="padding:10px;">
      <el-form :model="form" label-width="120px" @submit.prevent="onSubmit" size="mini" style="text-align:left;padding:10px;width:30%;min-width:200px;">
        <el-form-item label="漏洞名称名称">
          <el-input v-model="form.vul_name" placeholder="查询分组名称" size="small"></el-input>
        </el-form-item>
        <el-form-item label="运营字段">
          <el-input v-model="form.operator" placeholder="查询运营字段" size="small"></el-input>
        </el-form-item>
        <el-form-item label="审核人">
          <el-input v-model="form.aduitor" placeholder="查询运营字段" size="small"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" size="small">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-col :span="24" style="padding:0px 10px 10px 40px;">
      <el-table :data="tableData" stripe style="width:100%" border size="small" highlight-current-row>
        <el-table-column label="序号">
          <template slot-scope="scope">
            {{scope.$index+1}}
          </template>
        </el-table-column>
        <el-table-column prop="vul_name" label="漏洞名称">
        </el-table-column>
        <el-table-column prop="username" label="提交人">
        </el-table-column>
        <el-table-column prop="open_field" label="运营字段">
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间">
        </el-table-column>
        <el-table-column prop="auditor" label="审核人">
        </el-table-column>
        <el-table-column prop="update_time" label="审核时间">
        </el-table-column>
        <el-table-column label="操作" width="250">
          <template slot-scope="scope">
            <!-- <el-button @click="toverify(scope.row)">已审核</el-button> -->
            <el-button type="success" @click="dialogTableVisible = true,getresult(scope.row.audit_id)">已审核</el-button>
          </template>
          <el-dialog title="审核详情" :visible.sync="dialogTableVisible" width="30%" :before-close="handleClose">
               <span>漏洞名称：{{vul_name}}</span>
               <span>提交人：{{auditor}}</span>
               <span>提交时间：{{create_time}}</span>
               <span>提交内容：{{reason}}</span>
               <span>审核意见：审核通过</span>
               <span>评分：{{score}}</span>
               <span slot="footer" class="dialog-footer">
                 <el-button @click="dialogVisible = false">取 消</el-button>
                 <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
               </span>
              </el-dialog>
          <!--<template slot-scope="scope">
            <el-link :underline="false">账号列表</el-link>
          </template>-->
        </el-table-column> 
      </el-table>
    </el-col>
    <el-col :span="24" style="padding:20px 10px 10px 40px;text-align:left;">
      <el-pagination layout="total, prev, pager, next ,sizes ,jumper" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-size="20" :page-sizes="[10,20, 50, 100]" :total="total" style="float:left;">
      </el-pagination>
    </el-col>
  </section>
</template>
<script>
import {getauditinfos} from '../../api/api';
import {getauditresult} from '../../api/api';
export default {
  data() {
    return {
      form: {
        vul_name: '',
        operator: '',
        aduitor: ''
      },
      vul_name:'',
      auditor:'',
      create_time:'',
      reason:'',
      score:0,
      tableData: [],
      total:0,
      dialogTableVisible:false
    }
  },
  methods: {
    search: function() {
      let params={
        audit_status: 1,
        vul_name: this.form.vul_name,
        open_field: this.form.operator,
      };
      getauditinfos(params).then(res => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.tableData = result.result;
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    getresult(param){
     let params={audit_id:param};
     getauditresult(params).then(res => {
      this.vul_name  = res.data.result.vul_name;
      this.auditor = res.data.result.auditor;
      this.create_time = res.data.result.create_time;
      this.reason = res.data.result.reason;
      this.score = res.data.result.score;
    })
    },
  },
  created(){
    let params = {audit_status: 1};
    getauditinfos(params).then(res => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.tableData = result.result;
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
  }
}

</script>



