<template>
  <section>
    <el-col :span="24" style="margin-top:10px;">
      <el-tabs type="border-card" v-model="activeName">
        <el-tab-pane label="账号组" name="accountgroup">
          <accgroup @postgroup="change"></accgroup>
        </el-tab-pane>
        <el-tab-pane label="账号列表" name="accountlist">
          <acclist></acclist>
        </el-tab-pane>
      </el-tabs>
    </el-col>
  </section>
</template>
<script>
import accgroup from '@/components/accview/acc_group';
import acclist from '@/components/accview/acc_list'
export default {
  components: {
    accgroup,
    acclist,
  },
  data() {
    return {
      activeName: 'accountgroup',
    }
  },
  methods: {
    change: function(data) {
      this.activeName = 'accountlist'
    }
  },
}

</script>



