<template>
  <section>
    <!--列表-->
    <el-col :span="24" style="margin-bottom:0px;border-bottom:none;">
      <strong class="title" style="float:left;padding:10px">{{title}}</strong>
      <el-link :underline="false" href="#/vulshow" style="float:right;" class="linkbtn">更多 >></el-link>
      <el-table :data="tableData" style="width:100%;" border stripe size="small" highlight-current-row>
        <el-table-column type="selection" width="55" prop="select" v-if="select"></el-table-column>
        <el-table-column prop="vul_id" label="漏洞ID" width="160">
        </el-table-column>
        <el-table-column prop="find_date" label="发现时间" width="160">
        </el-table-column>
        <el-table-column prop="vul_name" label="漏洞名称">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="高危" placement="top">
              <img src="../../assets/img/icon/close_system3.gif" v-if="scope.row.severity==1">
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="中危" placement="top">
              <img src="../../assets/img/icon/close_system2.gif" v-if="scope.row.severity==2">
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="低危" placement="top">
              <img src="../../assets/img/icon/close_system1.gif" v-if="scope.row.severity==3">
            </el-tooltip>
            <el-link :underline="false" @click="getid(scope.row.id)">{{scope.row.vul_name}}</el-link>
          </template>
        </el-table-column>
        <el-table-column prop="vul_state" label="漏洞状态" width="220">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="修复建议" placement="top">
              <i class="el-icon-chat-line-square" v-if="scope.row.solution!=''"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="修复建议" placement="top">
              <i class="el-icon-chat-line-square" v-if="scope.row.solution==''" style="color:#ddd" :class="text-mix"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="CVE" placement="top">
              <i class="el-icon-tickets" v-if="scope.row.cve_id!=''"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="CVE" placement="top">
              <i class="el-icon-tickets" v-if="scope.row.cve_id==''" style="color:#ddd"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="WAF" placement="top">
              <i class="el-icon-cpu" v-if="scope.row.wafdefend==1"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="WAF" placement="top">
              <i class="el-icon-cpu" style="color:#ddd" v-if="scope.row.wafdefend==0"></i>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column prop="popularity_num" label="人气" width="120">
        </el-table-column>
        <el-table-column prop="comment_num" label="评论数" width="120">
        </el-table-column>
      </el-table>
    </el-col>
  </section>
</template>
<script>
import { getvul } from '../../api/api'
export default {
  props: {
    titletext: String
  },
  data() {
    return {
      select: false,
      title: this.titletext,
      tableData: [],
    }
  },
  created() {
    getvul({}).then(res => {
      let { status, message, result } = res.data;
      if (status == 2000) {
        if (this.title == '最新漏洞') {
          this.tableData = result.objects.newest_vuls
        }
        if (this.title == '热门漏洞') {
          this.tableData = result.objects.popular_vuls
        }
      }
    });
  },
  methods: {
    getid: function(id) {
      this.$router.push({
        path: '/vuldetail',
        name: 'vuldetail',
        query: {
          id: id,
          pgstatus: true
        }
      })
    },
  }
}

</script>
<style scoped>
.toolbar {
  padding: 10px 40px;
}

.linkbtn {
  font-size: 16px;
  padding: 10px;
}

.el-table--mini,
.el-table--small,
.el-table__expand-icon {
  font-size: 14px;
}

.el-table {
  color: #333;
  font-size: 14px;
}

.el-table>>>img {
  width: 18px;
  height: 18px;
  padding: 0;
  margin: 0;
  position: relative;
  top: 4px;
}

.el-table>>>.cell {
  text-align: center;
}

[class*=" el-icon-"],
[class^=el-icon-] {
  font-size: 18px;
  margin: 0px 2px;
  color: #1abc9c
}

</style>



