<template>
  <el-container style="height:100%">
    <el-header style="padding:0px;">
      <headertop></headertop>
    </el-header>
    <el-container>
      <el-main>
        <section>
          <el-col :span="24" class="toolbar">
            <strong style="line-height:30px;font-size:20px;margin:0px;padding:0px;font-weight:300;vertical-align:center;"> {{fodata.vul_name}}</strong>
            <p id="editime">最新编辑时间:{{update_time}}</p>
            <el-button type="text" icon="el-icon-star-off" size="small" style="float:right;vertical-align:top;margin-top:15px;padding-top:0;margin-right: 7px;">优先运营</el-button>
            <el-button type="text" icon="el-icon-star-off" size="small" style="float:right;vertical-align:top;margin-top:15px;padding-top:0;margin-right: 7px;">关注</el-button>
          </el-col>
          <el-col :span="24" style="margin-top:10px">
            <el-tabs type="border-card">
              <el-tab-pane label="漏洞详情">
                <el-form ref="form" :model="foadata" label-width="180px" @submit.prevent="onSubmit" style="margin:20px;width:50%;min-width:400px;text-align:left" size="mini" :disabled="pgstatus">
