                  </div>
                  <el-collapse-transition>
                    <div v-show="show1">
                      <el-form-item label="漏洞名称">
                        <el-input v-model="fodata.vul_name"></el-input>
                      </el-form-item>
                      <el-form-item label="扫描类型">
                        <el-select v-model="fodata.scan_type">
                          <el-option v-for="(item,index) in scan_type" :value="item.value" :key="index" :label="item.name"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="发现时间">
                        <el-date-picker type="date" placeholder="选择日期" v-model="fodata.find_date" style="width: 100%;" format="yyyy-MM-dd"></el-date-picker>
                      </el-form-item>
                      <el-form-item label="风险的等级">
                        <el-select v-model="fodata.severity" placeholder="请选择风险等级">
                          <el-option v-for="(item,index) in severity" :value="item.value" :key="index" :label="item.name"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="风险类型">
                        <el-input v-model="fodata.threaten_type"></el-input>
                      </el-form-item>
                      <el-form-item label="是否常见">
                        <el-select v-model="fodata.familiar" placeholder="是否常见">
                          <el-option v-for="(item,index) in familiar" :value="item.value" :key="index" :label="item.name"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="CVE编号">
                        <el-input v-model="fodata.cve_id"></el-input>
                      </el-form-item>
                      <el-form-item label="CVSS评分">
                        <el-input v-model="fodata.cvss_base_score"></el-input>
                      </el-form-item>
                      <el-form-item label="数据来源">
                        <el-select v-model="fodata.source" placeholder="请选择数据来源">
                          <el-option v-for="(item,index) in source" :value="item.value" :key="index" :label="item.name"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="WVSS危险插件">
                        <el-select v-model="fodata.wvss" placeholder="WVSS危险插件">
                          <el-option label="是" :value="1"></el-option>
                          <el-option label="否" :value="0"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="漏洞详情">
                        <el-input type="textarea" v-model="fodata.detail"></el-input>
                      </el-form-item>
                      <el-form-item label="修复建议">
                        <el-input type="textarea" v-model="fodata.solution" style="display:inline;"></el-input>
