                        <el-tag :key="tag" v-for="tag in dynamicTags" closable :disable-transitions="false" @close="handleClose(tag)">
                          {{tag}}
                        </el-tag>
                      </el-form-item>
                      <div class="collapsebtn" style="position:relative;left:20px;">
                        <i v-if="show2==true" class="el-icon-arrow-up" style="text-indent:10px;"></i><i v-else class="el-icon-arrow-down" style="text-indent:10px;"></i>
                        <el-link :underline="false" size="small" @click="show2=!show2">漏洞各种分类</el-link>
                      </div>
                      <el-collapse-transition>
                        <div v-if="show2">
                          <el-form-item label="WASC分类">
                            <el-input v-model="fodata.type.wasc_type"></el-input>
                          </el-form-item>
                          <el-form-item label="WASC2.0分类">
                            <el-input v-model="fodata.type.wasc2_type"></el-input>
                          </el-form-item>
                          <el-form-item label="OWASP-2010分类">
                            <el-input v-model="fodata.type.owasp2010_type"></el-input>
                          </el-form-item>
                          <el-form-item label="OWASP-2013分类">
                            <el-input v-model="fodata.type.owasp2013_type"></el-input>
                          </el-form-item>
                          <el-form-item label="应用分类">
                            <el-input v-model="fodata.type.pg_style2"></el-input>
                          </el-form-item>
                          <el-form-item label="服务分类">
                            <el-input v-model="fodata.type.pg_style1"></el-input>
                          </el-form-item>
                          <el-form-item label="操作系统分类">
                            <el-input v-model="fodata.type.pg_style3"></el-input>
                          </el-form-item>
                          <el-form-item label="CPE">
                            <el-input v-model="fodata.type.cpe"></el-input>
                          </el-form-item>
                        </div>
                      </el-collapse-transition>
