                          <el-form-item label="CNNVD ID">
                            <el-input v-model="fodata.ids.cnnvd"></el-input>
                          </el-form-item>
                          <el-form-item label="CNVD ID">
                            <el-input v-model="fodata.ids.cnvd"></el-input>
                          </el-form-item>
                          <el-form-item label="CNCVE ID">
                            <el-input v-model="fodata.ids.cncve"></el-input>
                          </el-form-item>
                          <el-form-item label="NSFOCUS ID">
                            <el-input v-model="fodata.ids.nsfocus_id"></el-input>
