                        <el-radio label="false">否</el-radio>
                      </el-radio-group>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button type="primary" size="small" @click="submitForm3('vul'),dialogFormVisible3 = false">确 定</el-button>
                    <el-button @click="dialogFormVisible3 = false" size="small">取 消</el-button>
                  </div>
                </el-dialog>
