                    <el-form-item label="通用">
                      <el-select v-model="vul.service_operate.common">
                        <el-option v-for="(item,index) in reinforce_level" :value="item.value" :key="index" :label="item.name"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item class="radio_f" label="是否交付验证">
                      <el-radio-group size="small" v-model="vul.is_deliver5">
                        <el-radio label="true">是</el-radio>
                        <el-radio label="false">否</el-radio>
                      </el-radio-group>
                    </el-form-item>
                    <el-form-item class="radio_s" label="是否测试验证">
                      <el-radio-group size="small" v-model="vul.is_test5">
                        <el-radio label="true">是</el-radio>
                        <el-radio label="false">否</el-radio>
                      </el-radio-group>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button type="primary" size="small" @click="submitForm5('vul'),dialogFormVisible5 = false,vul">确 定</el-button>
                    <el-button @click="dialogFormVisible5 = false" size="small">取 消</el-button>
                  </div>
                </el-dialog>
                <el-dialog :visible.sync="dialogFormVisible6" width="30%">
                  <el-form :model="vul" size="mini">
                    <el-form-item label="waf防护信息" :label-width="formLabelWidth">
                      <el-popover ref="popover" placement="bottom" title="WAF防护信息树型结构" width="400" trigger="focus" content="">
                        <el-tree class="filter-tree" :data="treedata" :props="defaultProps" show-checkbox node-key="id" ref="tree">
                        </el-tree>
                        <el-button @click="treeclick(),treeinfo=!treeinfo">选择完成</el-button>
                      </el-popover>
                      <el-input v-popover:popover :placeholder="this.vul.rule_type.join(',')"></el-input>
                    </el-form-item>
                    <el-form-item class="radio_f" label="是否交付验证">
                      <el-radio-group size="small" v-model="vul.is_deliver6">
                        <el-radio label="true">是</el-radio>
                        <el-radio label="false">否</el-radio>
                      </el-radio-group>
                    </el-form-item>
                    <el-form-item class="radio_s" label="是否测试验证">
                      <el-radio-group size="small" v-model="vul.is_test6">
                        <el-radio label="true">是</el-radio>
                        <el-radio label="false">否</el-radio>
                      </el-radio-group>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button type="primary" size="small" @click="submitForm6('vul'),dialogFormVisible6 = false">确 定</el-button>
                    <el-button @click="dialogFormVisible6 = false" size="small">取 消</el-button>
                  </div>
                </el-dialog>
                <el-col :span="24" style="margin-top:10px">          
                  <div>          
                    <commentpush></commentpush>          
                    <commentview></commentview>          
                  </div>          
                </el-col>
              </el-tab-pane>
              <el-tab-pane label="追加信息">
                <appendview></appendview>
              </el-tab-pane>
            </el-tabs>
          </el-col>
          
        </section>
      </el-main>
    </el-container>
    <el-footer style="padding:0px;">
      <footerbottom></footerbottom>
    </el-footer>
  </el-container>
</template>
<script>
import { forminit } from '../../api/api';
import { getvuldetail } from '../../api/api';
import { editvuldetail } from '../../api/api';
import { saveopenfields } from '../../api/api';
import commentview from '@/components/comment/comment_view';
import commentpush from '@/components/comment/comment_push';
import appendview from '@/components/append_view';
import headertop from '@/components/common/header';
import footerbottom from '@/components/common/footer'
export default {
  components: {
    commentpush,
    commentview,
    appendview,
    headertop,
    footerbottom
  },
  data() {
    return {
      update_time: '',
      formLabelWidth: '80px',
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      dialogFormVisible3: false,
      dialogFormVisible4: false,
      dialogFormVisible5: false,
      dialogFormVisible6: false,
      activeNames: ['1'],
      fodata: '',
      pgstatus: '',
      show1: true,
      show2: false,
      show3: false,
      show4: false,
      show5: false,
      show6: false,
      show7: false,
      treeinfo: false,
      treedata: [],
      defend_scan: [],
      defend_vul: [],
      continue_support: [],
      need_datum: [],
      measure: [],
      use_level: [],
      reinforce_level: [],
      dynamicTags: ['标签一', '标签二', '标签三', ],
      inputVisible: false,
      inputValue: '',
      vul: {
        field1: 2,
        field2: 4,
        field3: 5,
        field4: 6,
        field5: 6,
        field6: 1,
        is_deliver1: 'false',
        is_test1: 'false',
        is_deliver2: 'false',
        is_test2: 'false',
        is_deliver3: 'false',
        is_test3: 'false',
        is_deliver4: 'false',
        is_test4: 'false',
        is_deliver5: 'false',
        is_test5: 'false',
        is_deliver6: 'false',
        is_test6: 'false',
        solution: '',
        rule_type: [],
        service_operate: {
          use_way: '',
          misreport_scene: '',
          use_level: [],
          finance: [],
          operator: [],
          tobacco: [],
          energy: [],
          government: [],
        },
      }
    };
  },
  methods: {
    handleChange(val) {
      console.log(val);
    },
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },
    treeclick: function() {
      this.vul.rule_type = []
      this.$refs.tree.getCheckedNodes().forEach(item => {
        this.vul.rule_type.push(item.label)
      })
      this.vul.rule_type.join(',')
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = true;
      this.inputValue = '';
    },
    saveForm() {
      let params = {
        vul: this.fodata,
        id: this.$route.query.id
      }
      editvuldetail(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.$router.push('/vulshow')
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    restForm(formName) {
      this.$router.push('/vulshow')
    },
    submitForm1(form) {
      /*let params = {
        vul: this.vul,
        id: this.$route.query.id
      };
      editvuldetail(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.getvuldetail();
          }
        else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })*/
      let param = {
        //vul: this.vul,
        //id: this.$route.query.id
        operate_field: this.vul.field1,
        operate_content: this.vul.solution,
        vul_id: this.$route.query.id,
        is_deliver: this.vul.is_deliver1,
        is_test: this.vul.is_test1
      };
      saveopenfields(param).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          //this.getvuldetail();
          this.$message({
            showClose: true,
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    submitForm2(form) {
      /*let params = {
        vul: this.vul,
        id: this.$route.query.id
      };
      editvuldetail(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.getvuldetail();
          }
        else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
      */
      let param = {
        //vul: this.vul,
        //id: this.$route.query.id
        operate_field: this.vul.field2,
        operate_content: this.vul.service_operate.use_level,
        vul_id: this.$route.query.id,
        is_deliver: this.vul.is_deliver2,
        is_test: this.vul.is_test2
      };
      saveopenfields(param).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          //this.getvuldetail();
          this.$message({
            showClose: true,
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    submitForm3(form) {
      /*let params = {
        vul: this.vul,
        id: this.$route.query.id
      };
      editvuldetail(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.getvuldetail();
          }
        else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })*/
      let param = {
        //vul: this.vul,
        //id: this.$route.query.id
        operate_field: this.vul.field3,
        operate_content: this.vul.service_operate.use_way,
        vul_id: this.$route.query.id,
        is_deliver: this.vul.is_deliver3,
        is_test: this.vul.is_test3
      };
      saveopenfields(param).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          //this.getvuldetail();
          this.$message({
            showClose: true,
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    submitForm4(form) {
      /*let params = {
        vul: this.vul,
        id: this.$route.query.id
      };
      editvuldetail(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.getvuldetail();
          }
        else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })*/
      let param = {
        //vul: this.vul,
        //id: this.$route.query.id
        operate_field: this.vul.field4,
        operate_content: this.vul.service_operate.misreport_scene,
        vul_id: this.$route.query.id,
        is_deliver: this.vul.is_deliver4,
        is_test: this.vul.is_test4
      };
      saveopenfields(param).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          //this.getvuldetail();
          this.$message({
            showClose: true,
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    submitForm5(form) {
      /*let params = {
        vul: this.vul,
        id: this.$route.query.id
      };
      editvuldetail(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.getvuldetail();
          }
        else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })*/
      let param = {
        //vul: this.vul,
        //id: this.$route.query.id
        operate_field: this.vul.field5,
        operate_content: this.vul.service_operate,
        vul_id: this.$route.query.id,
        is_deliver: this.vul.is_deliver5,
        is_test: this.vul.is_test5
      };
      saveopenfields(param).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          //this.getvuldetail();
          this.$message({
            showClose: true,
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    submitForm6(form) {
      /*let params = {
        vul: this.vul,
        id: this.$route.query.id
      };
      editvuldetail(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.getvuldetail();
          }
        else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })*/
      let param = {
        //vul: this.vul,
        //id: this.$route.query.id
        operate_field: this.vul.field6,
        operate_content: this.vul.rule_type,
        vul_id: this.$route.query.id,
        is_deliver: this.vul.is_deliver6,
        is_test: this.vul.is_test6
      };
      saveopenfields(param).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          //this.getvuldetail();
          this.$message({
            showClose: true,
            message: '成功',
            type: 'success'
          })
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
  },
  created() {
    this.pgstatus = this.$route.query.pgstatus
    forminit().then((res) => {
      let { status, message, result } = res.data;
      if (status == 2000) {
        this.scan_type = result.objects.vul.scan_type;
        this.severity = result.objects.vul.severity;
        this.source = result.objects.vul.source;
        this.familiar = result.objects.vul.familiar;
        this.defend_scan = result.objects.soc_waf.defend_scan;
        this.defend_vul = result.objects.soc_waf.defend_vul;
        this.continue_support = result.objects.soc_waf.continue_support;
        this.need_datum = result.objects.soc_waf.need_datum;
        this.measure = result.objects.soc_waf.measure;
        this.use_level = result.objects.service.use_level;
        this.reinforce_level = result.objects.service.reinforce_level;
        result.objects.waf_defend.forEach(item => {
          if (item.pname == undefined && item.pId == 0) {
            let obj1 = {}
            obj1.id = item.id
            obj1.label = item.name
            obj1.children = []
            this.treedata.push(obj1)
            result.objects.waf_defend.forEach(item => {
              if (item.pname == undefined && item.pId == obj1.id) {
                let obj2 = {}
                obj2.id = item.id
                obj2.label = item.name
                obj2.children = []
                obj1.children.push(obj2)
                result.objects.waf_defend.forEach(item => {
                  if (item.pname != undefined && item.pname == obj2.label) {
                    let obj3 = {}
                    obj3.id = item.id
                    obj3.label = item.name
                    obj2.children.push(obj3)
                  }
                })
              }
            })
          }
        });
      }
    });
  },
  mounted() {
    let params = {
      id: this.$route.query.id
    };
    getvuldetail(params).then((res) => {
      let { status, message, result } = res.data;
      if (status == 2000) {
        console.log(result)
        this.fodata = result
        this.fodata.scan_type = Number(result.scan_type)
        this.fodata.severity = Number(result.severity)
        this.update_time = result.update_time.substring(0,19).replace('T', ' ');
      } else {
        console.log('default')
      }
    });
  },
}

</script>
<style scoped>
.el-form>>>.el-form-item__label {
  font-size: 12px;
}

.el-form-item.el-form-item--mini {
  margin-bottom: 10px;
}

.collapsebtn {
  padding:10px 0px ;
}

.el-icon-edit-outline {
  float: right;
  font-size: 16px;
  cursor: pointer;
}

.el-tag+.el-tag {
  margin-left: 10px;
  margin-bottom: 10px;
}

.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}

.input-new-tag {
  width: 90px;
  margin-left: 10px;
  margin-bottom: 10px;
  vertical-align: bottom;
}
#editime {
float: right;
font-size:13px;
color: rgb(32, 145, 157);
}
</style>



