<template>
  <section>
    <el-form ref="vul" :model="vul" :rules="rules" label-width="150px" @submit.prevent="onSubmit" style="margin:10px;width:60%;min-width:420px;text-align:left" size="mini">
      <div class="collapsebtn">
        <i v-if="show1==true" class="el-icon-arrow-up" style="text-indent:10px;"></i><i v-else class="el-icon-arrow-down" style="text-indent:10px;"></i>
        <el-link :underline="false" @click="show1=!show1">漏洞基本信息</el-link>
      </div>
      <el-collapse-transition>
        <div v-show="show1">
          <el-form-item label="漏洞名称" prop="vul_name">
            <el-input v-model="vul.vul_name" placeholder="请输入漏洞名称"></el-input>
          </el-form-item>
          <el-form-item label="扫描类型" prop="scan_type">
            <el-select v-model="vul.scan_type" placeholder="请选择扫描类型">
              <el-option v-for="item in scan_type" :value="item.value" :key="item.id" :label="item.name">{{item.name}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="漏洞ID" prop="vul_id">
            <el-input v-model="vul.vul_id" placeholder="请输入漏洞ID"></el-input>
          </el-form-item>
          <el-form-item label="发现时间" prop="find_date">
            <el-date-picker type="date" placeholder="选择日期" v-model="vul.find_date" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
          <el-form-item label="风险等级" prop="severity">
            <el-select v-model="vul.severity" placeholder="请选择风险等级">
              <el-option v-for="item in severity" :value="item.value" :key="item.id" :label="item.name">{{item.name}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="威胁类型" prop="threaten_type">
            <el-input v-model="vul.threaten_type" placeholder="威胁类型"></el-input>
          </el-form-item>
          <el-form-item label="是否常见" prop="familiar">
            <el-select v-model="vul.familiar" placeholder="是否常见">
              <el-option v-for="item in familiar" :value="item.value" :key="item.id" :label="item.name">{{item.name}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="CVE编号" prop="cve_id">
            <el-input v-model="vul.cve_id" maxlength="255" placeholder="请输入CVE编号"></el-input>
          </el-form-item>
          <el-form-item label="CVSS评分" prop="cvss_base_score">
            <el-input v-model="vul.cvss_base_score" placeholder="请输入CVSS评分" maxlength="255"></el-input>
          </el-form-item>
          <el-form-item label="数据来源" prop="source">
            <el-select v-model="vul.source" placeholder="请选择数据来源">
              <el-option v-for="item in source" :value="item.value" :key="item.id" :label="item.name">{{item.name}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="WVSS危险插件" prop="wvss">
            <el-select v-model="vul.wvss" placeholder="WVSS危险插件">
              <el-option label="是" value="1"></el-option>
              <el-option label="否" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="漏洞详情" prop="detail">
            <el-input type="textarea" v-model="vul.detail" placeholder="请输入漏洞详情"></el-input>
          </el-form-item>
          <el-form-item label="修复建议" prop="solution">
            <el-input type="textarea" v-model="vul.solution" placeholder="请输入修复建议"></el-input>
          </el-form-item>
          <el-form-item label="标签">
            <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm">
            </el-input>
            <el-button v-else class="button-new-tag" size="small" @click="showInput">+ 新标签</el-button>
            <el-divider content-position="center"><i class="el-icon-paperclip"></i> 所有标签</el-divider>
            <el-tag :key="tag" v-for="tag in dynamicTags" closable :disable-transitions="false" @close="handleClose(tag)">
              {{tag}}
            </el-tag>
          </el-form-item>
          <div class="collapsebtn" style="position:relative;left:20px;">
            <i v-if="show2==true" class="el-icon-arrow-up" style="text-indent:10px;"></i><i v-else class="el-icon-arrow-down" style="text-indent:10px;"></i>
            <el-link @click="show2 = !show2" size="small" :underline="false">漏洞的各种分类</el-link>
          </div>
          <el-collapse-transition>
            <div v-show="show2">
              <el-form-item label="WASC分类">
                <el-input v-model="vul.type.wasc_type" placeholder="请输入WASC分类"></el-input>
              </el-form-item>
              <el-form-item label="WASC2.0分类">
                <el-input v-model="vul.type.wasc2_type" placeholder="请输入WASC2.0分类"></el-input>
              </el-form-item>
              <el-form-item label="OWASP-2010分类">
                <el-input v-model="vul.type.owasp2010_type" placeholder="请输入OWASP-2010分类"></el-input>
              </el-form-item>
              <el-form-item label="OWASP-2013分类">
                <el-input v-model="vul.type.owasp2013_type" placeholder="请输入OWASP-2013分类"></el-input>
              </el-form-item>
              <el-form-item label="应用分类" prop="pg_style2">
                <el-input v-model="vul.type.pg_style2" placeholder="请输入应用分类"></el-input>
              </el-form-item>
              <el-form-item label="服务分类" prop="pg_style1">
                <el-input v-model="vul.type.pg_style1" placeholder="请输入服务分类"></el-input>
              </el-form-item>
              <el-form-item label="操作系统分类" prop="pg_style3">
                <el-input v-model="vul.type.pg_style3" placeholder="请输入操作系统分类"></el-input>
              </el-form-item>
              <el-form-item label="CPE">
                <el-input v-model="vul.type.cpe" placeholder="请输入CPE" maxlength="255"></el-input>
              </el-form-item>
            </div>
          </el-collapse-transition>
          <div class="collapsebtn" style="position:relative;left:20px;">
            <i v-if="show3==true" class="el-icon-arrow-up" style="text-indent:10px;"></i><i v-else class="el-icon-arrow-down" style="text-indent:10px;"></i>
            <el-link @click="show3 = !show3" size="small" :underline="false">漏洞的各种ID</el-link>
          </div>
          <el-collapse-transition>
            <div v-show="show3">
              <el-form-item label="CNNVD ID">
                <el-input v-model="vul.ids.cnnvd" maxlength="255" placeholder="请输入CNNVD ID"></el-input>
              </el-form-item>
              <el-form-item label="CNVD ID">
                <el-input v-model="vul.ids.cnvd" maxlength="255" placeholder="请输入CNVD ID"></el-input>
              </el-form-item>
              <el-form-item label="CNCVE ID">
                <el-input v-model="vul.ids.cncve" maxlength="255" placeholder="请输入CNCVE ID"></el-input>
              </el-form-item>
              <el-form-item label="NSFOCUS ID">
                <el-input v-model="vul.ids.nsfocus_id" maxlength="255" placeholder="请输入NSFOCUS ID"></el-input>
              </el-form-item>
              <el-form-item label="BUGTRAQ ID">
                <el-input v-model="vul.ids.bugtraq_id" maxlength="255" placeholder="请输入BUGTRAQ ID"></el-input>
              </el-form-item>
              <el-form-item label="AWVS ID">
                <el-input v-model="vul.ids.awvs_id" placeholder="请输入AWVS"></el-input>
              </el-form-item>
              <el-form-item label="AWVS漏洞名称">
                <el-input v-model="vul.ids.awvs_name" placeholder="请输入AWVS漏洞名称"></el-input>
              </el-form-item>
            </div>
          </el-collapse-transition>
        </div>
      </el-collapse-transition>
      <div class="collapsebtn">
        <i v-if="show4==true" class="el-icon-arrow-up" style="text-indent:10px;"></i><i v-else class="el-icon-arrow-down" style="text-indent:10px;"></i>
        <el-link @click="show4 = !show4" size="small" :underline="false">插件组</el-link>
      </div>
      <el-collapse-transition>
        <div v-show="show4">
          <el-form-item label="漏洞验证环境">
            <el-input v-model="vul.environment"></el-input>
          </el-form-item>
        </div>
      </el-collapse-transition>
      <el-form-item>
        <el-button type="primary" size="small" @click="submitForm('vul')">保存</el-button>
        <el-button size="small" @click="restForm('vul')">取消</el-button>
      </el-form-item>
    </el-form>
    </el-col>
  </section>
</template>
<script>
import { addvul, forminit } from '../api/api'
export default {
  data() {
    return {
      show1: true,
      show2: true,
      show3: true,
      show4: true,
      vul: {
        vul_name: '',
        scan_type: '',
        vul_id: '',
        find_date: '',
        severity: '',
        threaten_type: '',
        familiar: '',
        cve_id: '',
        cvss_base_score: '',
        source: '',
        wvss: '',
        detail: '',
        solution: '',
        tigs: '',
        type: {
          wasc_type: '',
          wasc2_type: '',
          owasp2010_type: '',
          owasp2013_type: '',
          pg_style2: '',
          pg_style1: '',
          pg_style3: '',
          cpe: '',
        },
        ids: {
          cnnvd: '',
          cnvd: '',
          cncve: '',
          nsfocus_id: '',
          bugtraq_id: '',
          awvs_id: '',
          awvs_name: '',
        },
        environment: '',
      },
      scan_type: [],
      severity: [],
      source: [],
      familiar: [],
      rules: {
        vul_name: [
          { required: true, message: '请输入漏洞名称', trigger: 'blur' },
          { min: 0, max: 255, message: '长度应小于255', trigger: 'blur' }
        ],
        scan_type: [
          { required: true, message: '请输入扫描类型', trigger: 'change' }
        ],
        vul_id: [
          { required: true, message: '请输入漏洞id', trigger: 'blur' },
          { min: 0, max: 255, message: '长度应小于255', trigger: 'blur' }
        ],
        /*date: [
          { required: true, message: '请选择发现时间', trigger: 'blur' }
        ],*/
        severity: [
          { required: true, message: '请选择风险等级', trigger: 'change' }
        ],
        threaten_type: [
          { required: true, message: '请输入威胁类型', trigger: 'blur' },
          { min: 0, max: 255, message: '长度应小于255', trigger: 'blur' }
        ],
        familiar: [
          { required: true, message: '请选择是否常见', trigger: 'change' }
        ],
        cve_id: [
          { min: 0, max: 255, message: '长度应小于255', trigger: 'blur' }
        ],
        cvss_base_score: [
          { min: 0, max: 255, message: '长度应小于255', trigger: 'blur' }
        ],
        source: [
          { required: true, message: '请选择数据来源', trigger: 'change' }
        ],
        detail: [
          { required: true, message: '请输入漏洞详情', trigger: 'blur' }
        ],
        solution: [
          { required: true, message: '请输入修复建议', trigger: 'blur' }
        ],
        /*pg_style2: [
          { required: true, message: '请输入应用分类', trigger: 'blur' }
        ],
        pg_style1: [
          { required: true, message: '请输入服务分类', trigger: 'blur' }
        ],
        pg_style3: [
          { required: true, message: '请输入操作系统分类', trigger: 'blur' }
        ],*/
      },
      dynamicTags: ['标签一', '标签二', '标签三', ],
      inputVisible: false,
      inputValue: '',
      activeNames: ['1']
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let addvulParams = {
            vul: this.vul
          };
          var para = JSON.stringify(addvulParams)
          addvul(para).then(data => {
            console.log(data);
            let { status, message } = data;
            if (status == 2000) {
              console.log('success')
              this.$router.push('/home');
            } else {
              console.log('default')
            }
          })
        } else {
          console.log('error submit');
          return true;
        }
      });
    },
    restForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = true;
      this.inputValue = '';
    }
  },
  created() {
    forminit().then((res) => {
      let { status, message, result } = res.data;
      if (status == 2000) {
        console.log(result.objects);
        this.scan_type = result.objects.vul.scan_type;
        this.severity = result.objects.vul.severity;
        this.source = result.objects.vul.source;
        this.familiar = result.objects.vul.familiar;
      }
    });
  }
}

</script>
<style scoped>
.el-tag+.el-tag {
  margin-left: 10px;
  margin-bottom: 10px;
}

.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}

.input-new-tag {
  width: 90px;
  margin-left: 10px;
  margin-bottom: 10px;
  vertical-align: bottom;
}

.collapsebtn {
  padding:10px 0px 10px 0px;
}

</style>



