<template>
  <div id="app" @click="handleTime()">
    <router-view />
  </div>
</template>
<script>
export default {
  name: 'App',
  data() {
    return {
      currentTime: new Date().getTime()
    }
  },
  methods: {
    handleTime() {
      let currentTime = this.currentTime
      let lastTime = new Date().getTime()
      let timeout = 60 * 60 * 1000
      if (lastTime - currentTime > timeout) {
        this.currentTime = new Date().getTime()
        sessionStorage.removeItem('user');
        this.$router.push('/login')
        this.$message({
          showClose: true,
          message: '系统监测到您一小时没有任何操作，请重新登录',
          type: 'warning'
        })
      } else {
        this.currentTime = new Date().getTime()
      }
    }
  },
}

</script>
<style>
/*-----------------------------------------default-------*/
body,
html {
  width: 100%;
  height: 100%;
  padding: 0px;
  margin: 0px;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 0px;
  height: 100%;
}

.el-main {
  padding: 0 0;
}

/*-----------------------------------------common-------*/
.el-table_7_column_41 {
  text-align: left;
}

.toolbar {
  background: #fff;
  padding: 15px;
  border: 1px solid #dfe6ec;
  margin: 10px 0px 0px 0px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

/*----------------------common formbox style----------------*/

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

/*--------------------------fade------------------------------*/
.fade-enter-active,
.fade-leave-active {
  transition: all .3s ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

/*------------------------comment------------------------------*/
.comment {
  border-top: 1px solid #e3e3e3;
  padding: 0px 20px;
  font-size: 14px;
}

.commenttools {
  padding: 0px 10px;
}
::-webkit-scrollbar {
  width: 0 !important;
}
::-webkit-scrollbar {
  width: 0 !important;
  height: 0;
}
</style>



