<template>
  <div style="background-color: #34495e; padding-top: 0px;">
    <el-form ref="form" :model="form" label-width="80px">
      <div class="content">
        <h2 style="color:whitesmoke; font-weight: bolder; font-size: 28px">环境信息数据统计表</h2>
        <div class="data">
          <el-form-item label="开始时间" class='startItem'>
            <el-date-picker
              v-model="form.dater"
              type="datetime"
              :picker-options="startTime"
              placeholder="开始日期"
              style="margin-left: 20px;font-size: 16px; color: whitesmoke"
            ></el-date-picker>
          </el-form-item>

          <el-form-item label="结束时间" class='startItem'>
            <el-date-picker
              v-model="form.end"
              type="datetime"
              :picker-options="endTime"
              placeholder="结束日期"
              style="margin-left: 20px;font-size: 16px; color: whitesmoke"
            ></el-date-picker>
          </el-form-item>
          <el-button type="primary" size="middle" style="margin-left: 5%; height: 42px;" @click="searchDate">查询</el-button>
        </div>

        <div class="derive">
          <el-select v-model="form.value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button style="margin-left:2%;" icon="el-icon-printer">一键导出</el-button>
          <el-input
            style="width: 220px;margin-left: 23.5%;"
            icon="el-icon-search"
            v-model="form.search"
            placeholder="请输入内容"
          ></el-input>
          <el-button size="small" style="margin-left: 1.5%;" icon="el-icon-menu"></el-button>
        </div>
        <el-table
          :data="tableData.slice((currentPage-1)*pageSize, currentPage*pageSize).filter(data => !form.search || data.name.toLowerCase().includes(form.search.toLowerCase()))"
          border
          style="width: 100%;"
        >
          <!-- 多选框 -->
          <!--<el-table-column
                      type="selection"
                      width="55">
          </el-table-column>-->
          <!-- <el-table-column v-for="(th, key) in tableHeader"
                      min-height="46"
                      :key="key"
                      :prop="th.prop"
                      :label="th.label"
                      :fixed="th.fixed"
                      :sortable="th.custom?'custom':false"
                      :min-width="th.minWidth" align="center">
          </el-table-column>-->
          <el-table-column prop="name" label="Sensor" />
          <el-table-column prop="data" label="时间" />
          <el-table-column prop="wendu" label="温度" />
          <el-table-column prop="shidu" label="湿度" />
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[3,5,10,20]"
            :page-count="5"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="tableData.length"
          ></el-pagination>
        </div>
      </div>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      input: "",
      total: 0,
      currentPage:1,
      pageSize:3,
      searUser: {},

      form: {
        dater: "",
        end: "",
        value: "1",
        search: "",
      },
      startTime: {
        disabledDate: (time) => {
          if (this.form.end) {
            return time.getTime() > new Date(this.form.end).getTime();
          } else {
            return time.getTime() > Date.now();
          }
        },
      },
      endTime: {
        disabledDate: (time) => {
          if (this.form.dater) {
            return (
              time.getTime() > Date.now() ||
              time.getTime() < new Date(this.form.dater).getTime()
            );
          } else {
            return time.getTime() > Date.now();
          }
        },
      },
      datas: [
        {
          name: "m0219",
          data: "2018-10-29",
          wendu: "30",
          shidu: "28",
        },
        {
          name: "m0218",
          data: "2018-11-20",
          wendu: "34",
          shidu: "26",
        },
        {
          name: "m0217",
          data: "2018-11-20",
          wendu: "34",
          shidu: "26",
        },
        {
          name: "m0216",
          data: "2018-11-20",
          wendu: "34",
          shidu: "26",
        },
        {
          name: "m0215",
          data: "2018-11-20",
          wendu: "34",
          shidu: "26",
        },
        {
          name: "m0214",
          data: "2018-11-20",
          wendu: "34",
          shidu: "26",
        },
      ],

      options: [
        {
          label: "导出当前页面数据",
          value: "1",
        },
        {
          label: "导出所有页面数据",
          value: "2",
        },
      ],

      pageIndex: 1,
      multipleSelection: [], // 多行选中
    };
  },
  methods: {
    handleClick(row) {
      console.log(row);
    },
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getPageData();
    },
    searchKeywords() {},
    getList() {},
    // 多行选中
    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.$emit("handleSelectionChange", val);
    },

    getPageData(){
      this.datas.slice((this.currentPage-1) * this.pageSize, this.currentPage * this.pageSize)
    },
  },
  computed: {
    tableData() {
      const search = this.search;
      if (search) {
        // filter() 方法创建一个新的数组，新数组中的元素是通过检查指定数组中符合条件的所有元素。
        // 注意： filter() 不会对空数组进行检测。
        // 注意： filter() 不会改变原始数组。
        return this.datas.filter((data) => {
          // some() 方法用于检测数组中的元素是否满足指定条件;
          // some() 方法会依次执行数组的每个元素：
          // 如果有一个元素满足条件，则表达式返回true , 剩余的元素不会再执行检测;
          // 如果没有满足条件的元素，则返回false。
          // 注意： some() 不会对空数组进行检测。
          // 注意： some() 不会改变原始数组。
          return Object.keys(data).some((key) => {
            //Object.keys()---如果处理对象，将返回一个可进行枚举的属性数组，注意，是返回一个数组，是通过属性名组成的数组
            //Object.keys()---参数：要返回其枚举自身属性的对象；  返回值：一个表示给定对象的所有可枚举属性的字符串数组
            // indexOf() 返回某个指定的字符在某个字符串中首次出现的位置，如果没有找到就返回-1；
            // 该方法对大小写敏感！所以之前需要toLowerCase()方法将所有查询到内容变为小写。
            return String(data[key]).toLowerCase().indexOf(search) > -1;
          });
        });
      }
      return this.datas;
    },
  },
};
</script>

<style scoped>
.content {
  /* width: 60%; */
  border-radius: 18px;
  margin: 20px auto;
  border: 1px solid silver;
}
.content > span {
  font-size: 16px;
  display: inline-block;
  width: 100%;
  text-align: center;
  padding: 20px 0;
}
.derive {
  display: flex;
  padding: 10px 109px;
}
.block {
  padding: 16px 22px;
}
.data {
  justify-content: space-around;
  display: flex;
}

</style>
<style>
  .startItem .el-form-item__label {
    color: white;
  }

  .el-pagination__total{
    color: whitesmoke;
  }
  .el-pagination__jump{
    color: whitesmoke;
  }

</style>



