<template>
  <section>
  <el-col :span="24" style="padding:0px 10px 10px 40px;">
      <el-table :data="tableData" stripe style="width:100%" row-style="height:50px" border size="small" highlight-current-row>
        <el-table-column type="index" width="50" label="序号">
        </el-table-column> 
        <el-table-column prop="open_field" label="积分项" width="160">
        </el-table-column>
        <el-table-column prop="score" label="积分值" width="160">
        </el-table-column>
        <el-table-column prop="create_time" label="积分日期" width="280">
        </el-table-column>
        <el-table-column prop="vul_name" label="漏洞名称">
        </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="24" style="padding:20px 10px 10px 40px;text-align:left;">
      <el-pagination layout="total, prev, pager, next ,sizes ,jumper" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-size="20" :page-sizes="[1,20, 50, 100]" :total="total" style="float:left;">
      </el-pagination>
    </el-col>
  </section>
</template>
<script>
import { getallscores } from '../../api/api'
export default {
  data() {
    return {
	      tableData:[],
      total:0,
      pagenum: 20,
      page: 1,
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.page = val;
      this.getacclist()
    },
    handleSizeChange(val) {
      this.pagenum = val;
      this.getacclist()
    },
    getacclist(obj, name) {
      let params = {
        page: this.page,
        pagenum: this.pagenum
      }
      Object.assign(params, obj)
      params.name = this.name
      getaccoutlist(params).then((res) => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.total = result.total
          this.tableData = result.result
        } else {
          this.total = 1
        }
      })
    }
    
  },
  created(){
    getallscores(params).then((res) => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.total = result.total
          this.tableData = result.result
        } else {
          this.total = 1
        }
      })
  }
}

</script>
<style scoped>
.el-table {
  color: #333;
  font-size: 14px;
}

.el-table>>>.cell {
  text-align: center;
}

</style>



