import vullist from "@/components/personinfoview/vul_list";
import Highcharts from "highcharts/highstock";
import exportingInit from "highcharts/modules/exporting";
import heatRoom from "@/components/heat_room";
import humiRoom from "@/components/humidity_room";
import XChart from "@/components/roomcharts/chart";
import XChart1 from "@/components/roomcharts/chart1";
import XChart2 from "@/components/roomcharts/chart2";
import XChart3 from "@/components/roomcharts/chart3";
import Table from "@/components/roomcharts/index";
exportingInit(Highcharts);
// import "static/bootstrap-3.3.7-dist/css/bootstrap.min.css";
// import "/static/bootstrap-datetimepicker-master/css/bootstrap-datetimepicker.min.css";

export default {
  // name: 'Echarts',
  data() {
    return {
      src: "time1.js",
      currTime: new Date(), //获取到的时间
      updateTimer: null, // 更新时钟的timer
      millisecond: false, //毫秒
    };
  },
  mounted() {
    // this.myEcharts();]
    // document.getElementById('real-time').value = this.currentTime
    document.getElementById("real-time").value = this.timeDisplay();
    // clearInterval(this.updateTimer)
    // this._cache_ = setInterval(this.currTime, 1000);
  },
  created() {
    this.currTime = new Date();
    clearInterval(this.updateTimer);
    this.updateTimer = setInterval(() => {
      this.currTime = new Date();
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.updateTimer);
  },
  computed: {},
  methods: {
    timeDisplay() {
      // 时间展示区
      let years = this.currTime.getFullYear();
      let months = this.currTime.getMonth() + 1;
      let days = this.currTime.getDate();

      let hours =
        this.currTime.getHours() > 9
          ? this.currTime.getHours()
          : "0" + this.currTime.getHours();
      let minutes =
        this.currTime.getMinutes() > 9
          ? this.currTime.getMinutes()
          : "0" + this.currTime.getMinutes();
      let seconds =
        this.currTime.getSeconds() > 9
          ? this.currTime.getSeconds()
          : "0" + this.currTime.getSeconds();
      this.currTime =
        years +
        "/" +
        months +
        "/" +
        days +
        "  " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds;
      return (
        years +
        "/" +
        months +
        "/" +
        days +
        "  " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds
      );
    },
  },
  components: {
    heatRoom,
    humiRoom,
    XChart,
    XChart1,
    XChart2,
    XChart3,
    Table,
  },
};
</script>
