import Heatmap from 'heatmap.js';
export default {
  mounted: function () {
    // 正面一
    var heatmapInstance1 = Heatmap.create({
      container: document.getElementById('heatmap1')
    });
    // 构建一些随机数据点,网页切图价格这里替换成你的业务数据
    var points1 = [];
    var max1 = 20;
    var min1 = 0;
    var val1=[];
    for (var j = 0; j < 4; j++){
      for (var i = 0; i < 5; i++){
        var temp = Math.random()*20
        // var temp = 20
        var point = {
          x: 100 + i*100,
          y: 60 + j*60,
          value: temp,
          radius:100,
        }
        points1.push(point)
      }
    }
    // console.log(points1);
    var data1 = {
      max: max1,
      data: points1
    }
    heatmapInstance1.setData(data1)

