    });
    // 构建一些随机数据点,网页切图价格这里替换成你的业务数据
    var points4 = [];
    var max4 = 20;
    var min4 = 0;
    var val4 =[];
    for (var j = 0; j < 4; j++){
      for (var i = 0; i < 5; i++){
