<template>
  <div class="home">
    <div class="container-flex" tabindex="0" hidefocus="true" :style="note">
      <div class="box-left" :style="box" style="margin-top:90px;">
        <div class="left-top" style="margin-top: 0px;">
          <div class="current-num" :style="box1">
            <div :style="box2">当前人数</div>
            <p style="font-size: 60px;">50</p>
          </div>
        </div>
        <div class="left-top" style="margin-top:55px;">
          <div class="title-box" :style="box3">
            <h3 style="margin-left:190px;font-size: 24px;font-weight: 530;">主入口</h3>
          </div>
          <div class="chart-box1" style="margin-top: 20;margin-right:60px">
            <video width="100%" autoplay="autoplay" controls="controls" style="margin-top:60px;">
              <source src="../../assets/images/results3.mp4" type="video/mp4" />
              <source />
            </video>
          </div>
        </div>
        <div class="right-bottom" style="margin-top:160px;">
          <div class="title-box" :style="box3">
            <h3 style="margin-left:120px;font-size: 24px;font-weight: 530;">主入口人员身份识别</h3>
          </div>
          <div class="chart-box1" style="margin-right:75px">
            <img style="margin-top: 30px;margin-left:15px;" src="../../assets/identification/demo1.jpg" alt width="100%" />
          </div>
        </div>
      </div>
      <div class="box-center">
        <div class="center-top" :style="box4">
          <h1 style="text-align: center;margin-top: 6px;font-size: 40px;margin-bottom: 6px;border-bottom: 0px solid #ccc;padding: 2px 0 20px 30px;color:#cdddf7;letter-spacing: 8px;">人员身份识别平台</h1>
        </div>
        <el-button style="float: left;background-color: #111c4b;border: 1px ;margin-left:-460px;margin-top:-55px;" type="primary" icon="el-icon-s-home" @click="home">返回</el-button>
        <div class="center-center">
          <div style="float:right;margin-top:-180px;margin-right:0px;">
            <el-button-group>
              <el-button type="primary" icon="el-icon-arrow-left" @click="before" style="background-color: #0c2c5e;border: 1px ;">上一页</el-button>
            </el-button-group>
          </div>
          <img src="../../assets/images/line_bg.png" alt style="margin-right:20px" />
          <div class="weather-box">
            <div class="center" id="clock1">
              <clockShow></clockShow>
            </div>
          </div>
          <img src="../../assets/images/line_bg.png" alt style="margin-left:20px" />
          <div style="float:right;margin-top:-180px;margin-right:2px;">
            <el-button-group>
              <el-button type="primary" @click="next" style="background-color: #0c2c5e;border: 1px ;">
                下一页
                <i class="el-icon-arrow-right el-icon--right"></i>
              </el-button>
            </el-button-group>
          </div>
        </div>
        <div class="center-bottom">
          <div class="chart-box" style="margin-top:20px">
            <img
              class="bj-person"
              style="width: 111%;padding:0;background: url(../../assets/fivefloor2.png);"
              src="../../assets/fivefloor2.png"
            />
            <!-- <div id="container" style="min-width:400px;height:400px">
              <highcharts :options="chartOptions" :callback="myCallback"></highcharts>
            </div> -->
          </div>
        </div>
      </div>
      <div class="box-right" :style="box5" style="margin-top:90px;">
        <div class="left-top" style="margin-left:10px;">
          <div class="current-num" :style="box1">
            <div :style="box2">当前访客数</div>
            <p style="font-size: 60px;">35</p>
          </div>
        </div>
        <div class="left-top" style="margin-top:55px;margin-left:10px">
          <div class="title-box" :style="box3">
            <h3 style="margin-left:190px;font-size: 24px;font-weight: 530;">次入口</h3>
          </div>
          <div class="chart-box1" style="margin-top: 20;margin-right:55px">
            <video width="100%" autoplay="autoplay" controls="controls" style="margin-top:60px;">
              <source src="../../assets/images/results3.mp4" type="video/mp4" />
              <source />
            </video>
          </div>
        </div>
        <div class="right-bottom" style="margin-top:160px;margin-left:10px">
          <div class="title-box" :style="box3">
            <h3 style="margin-left:120px;font-size: 24px;font-weight: 530;">次入口人员身份识别</h3>
          </div>
          <div class="chart-box1" style="margin-right:70px">
            <img style="margin-top: 45px;margin-left:15px;" src="../../assets/identification/demo2.jpg" alt width="100%" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Homepie from "@/components/homecharts/homepie";
import Homemix from "@/components/homecharts/homemix";
import clockShow from "@/components/time/clock1";
export default {
  components: {
    Homepie,
    Homemix,
    clockShow
  },
  data() {
    return {
      box1: {
        backgroundImage:
          "url(" + require("../../assets/images/border_bg01.png") + ")"
      },
      box2: {
        backgroundImage:
          "url(" + require("../../assets/images/title_bg01.png") + ")"
      },
      box: {
        backgroundImage:
          "url(" + require("../../assets/images/line_img.png") + ")"
      },
      note: {
        backgroundImage:
          "url(" + require("../../assets/picture/bg.png") + ")"
        // marginTop: "5px",
      },
      box3: {
        backgroundImage:
          "url(" + require("../../assets/images/box_title.png") + ")"
      },
      playerOptions1: {
        height: "360",
        autoplay: true,
        muted: true,
        language: "en",
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "video/mp4",
            // mp4
            src: require("../../assets/images/results3.mp4")
            // webm
            // src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
          }
        ],
        poster:
          "https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg"
      },
      playerOptions2: {
        height: "360",
        autoplay: true,
        muted: true,
        language: "en",
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "video/mp4",
            // mp4
            src: require("../../assets/images/results2.mp4")
            // webm
            // src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
          }
        ],
        poster:
          "https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg"
      },
      box4: {
        backgroundImage:
          "url(" + require("../../assets/imp/title.png") + ")",
      },
      box5: {
        backgroundImage:
          "url(" + require("../../assets/images/line_img.png") + ")"
      },
      date: new Date(),
      chartOptions: {
        chart: {
          type: "pie",
          height: 320,
          backgroundColor: "none",
          options3d: {
            enabled: true,
            alpha: 45,
            beta: 0
          }
        },
        legend: {
          align: "right", //程度标的目标地位
          verticalAlign: "top", //垂直标的目标地位
          enabled: false,

          style:{color:'white'}
        },
        credits: {
          enabled: false
        },
        title: {
          text: "当前不同访客数量占比",
          style: {
            color: "white",
            fontSize:'25px'
          }
        },
        tooltip: {
          pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>"
        },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: "pointer",
            depth: 35,
            dataLabels: {
              enabled: true,
              format: "{point.name}",
              style: {
                fontSize: 20,
                color:'#cdddf7'
              }
            }
          }
        },
        series: [
          {
            type: "pie",
            name: "访客种类占比",
            data: [
              ["楼内人员", 45.0],
              
              {
                name: "访客",
                y: 55.0,
                sliced: true,
                selected: true
              }
            ]
          }
        ]
      }
    };
  },
  created() {},
  methods: {
    myCallback() {
      console.log("this is callback function");
    },
    next() {
      this.$router.push("/positioning");
    },
    before() {
      this.$router.push("/home");
    },
    home() {
      this.$router.push("/mainpage");
    },
  },
  mounted() {
    let _this = this; // 声明一个变量指向Vue实例this，保证作用域一致
    this.timer = setInterval(() => {
      _this.date = new Date(); // 修改数据date
    }, 1000);
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
    }
  }
};
</script>



<style scoped>
.home {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0;
  padding: 0;
  background: #051655;
  font-family: 微软雅黑,  MicrosoftYahei,  sans-serif;
  color: #fff;
}

.center {
  position: relative;
  width: 65%;
}

#clock1 {
  position: relative;
  font-size: 24px;
  opacity: 1;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0%;
  /* margin-left: 27%; */
  /*margin-top: 20%;*/
  /*height: 100px;*/
  background-size: 80%;

  z-index: 900;
}

.highcharts-container {
  width: 600px;
  height: 400px;
  border: 0px solid #ddd;
  margin: auto;
}

#container {
  margin-top: -50px;
  height: 300px;
}

.container-flex {
  width: 100%;
  height: 100%;
  background: left top repeat;
  background-size: 100% 100%;
  display: flex;
  flex-flow: row nowrap;
}

.box-left {
  width: 28%;
  height: 100%;
  margin-top: 50px;
  background:  right repeat-y;
}
.left-top {
  width: 100%;
  height: 20.4%;
  position: relative;
}

.left-top > .current-num {
  width: 85%;
  height: 100%;
  position: absolute;
  margin: auto;
  top: 10px;
  left: 0;
  bottom: 0;
  right: 0;
  background: top left no-repeat;
  background-size: 100% 100%;
}

.current-num > div {
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  background: center center no-repeat;
  font-size: 1.9rem;
  letter-spacing: 3px;
  color: #e7eaed;
  background-size: 1.8rem 0.25rem;
  font-weight: 900;
  margin-top: 30px;
}

.current-num > p {
  font-size: 2.5rem;
  text-align: center;
  color: white;
}

.title-box {
  width: 90%;
  height: 2.42rem;
  background: top left no-repeat;
  background-size: 100% 100%;
  margin: auto;
  margin-left: 20px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  position: relative;
}

.title-box > h6,
.title-box > p {
  font-size: 0.2rem;
  color: white;
  height: 0.42rem;
  line-height: 0.42rem;
  margin-left: 0.4rem;
}

.title-box > p > span {
  cursor: pointer;
  color: rgba(255, 255, 255, 0.6);
}

.title-box > p > span.active {
  color: rgba(255, 255, 255, 1);
}

.chart-box {
  position: absolute;
  top: 0.66rem;
  bottom: 0px;
  width: 90%;
  left: 0;
  right: 0;
  height: 114px;
}
.chart-box1 {
  height: 114px;
  width: 400px;
  height: 400px;
  margin: 0 auto;
}
.right-bottom {
  width: 100%;
  height: 30%;
  position: relative;
}

.box-center {
  width: 50%;
  height: 100%;
  /*    background: green;*/
}

.center-top {
  width: 180%;
  height: 8.8%;
  position: relative;
  top: 0;
  margin-left: -362px;
  
  /* background: bottom center no-repeat; */
  background:  center no-repeat;

  /* display: flex; */
  /* justify-content: center; */
  align-items: center;
}

.center-center {
  width: 100%;
  height: 15.3%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.weather-box {
  width: 51%;
  height: 83%;
  border: 1px solid #0e94ea;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
}

.weather-box > .data {
  width: 2.19rem;
  height: 0.65rem;
  border-right: 2px solid #cdddf7;
}

.data > p {
  font-size: 3rem;
  margin: 0 5px;
  color: #cdddf7;
  text-align: center;
  margin: 0;
}

.data > p > span {
  margin: 0 5px;
}

.data > p.time {
  font-size: 10px;
  height: 0.42rem;
  line-height: 0.42rem;
}

.weather-box > .weather {
  width: 1.8rem;
  height: 0.65rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  margin-left: 10px;
}

.weather > img {
  height: 400%;
}

.weather > div {
  width: 0.8rem;
  height: 100%;
}

.weather > div > p {
  font-size: 0.12rem;
  color: #cdddf7;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 0.24rem;
  line-height: 0.24rem;
}

.weather > div > p.active {
  color: white;
  font-size: 0.16rem;
}

.center-bottom {
  width: 100%;
  height: 75.9%;
  position: relative;
}

.center-bottom > .city-data {
  width: 8.05rem;
  height: 100%;
  margin: auto;
  position: absolute;
}

.box-right {
  width: 28%;
  height: 100%;
  margin-top: 20px;
  background: top left repeat-y;
  margin-top: 50px;

}

.video-player {
  height: 10px !important;
}

.vjs-custom-skin {
  height: 170px !important;
}
</style>


