import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/page/layout'
import Layout_room1 from '@/page/layout_room1'
import Layout_room2 from '@/page/layout_room2'
import Layout_room3 from '@/page/layout_room3'
import Layout_room4 from '@/page/layout_room4'
import Layout_room5 from '@/page/layout_room5'
import Layout_room6 from '@/page/layout_room6'
import Home from '@/page/home/home'
import Mainpage from '@/page/home/mainpage'
import Efficiency from '@/page/home/efficiency'
import Identification from '@/page/home/identification'
import Positioning from '@/page/home/positioning'
import Evacuation from '@/page/home/evacuation'
import Iron from '@/page/home/iron'
import HEnergy from '@/page/home/Henergy'
import Carbuilding from '@/page/home/carbuilding'
import Fivefloor from '@/page/home/fivefloor'
import Vul_show from '@/page/vul_show/vul_show'
import Vul_add from '@/page/vul_show/vul_add'
import Vul_detail from '@/page/vul_show/vul_detail'
import Login from '@/page/login/login'
import Account from '@/page/account/account'
import Myinfo from '@/page/personinfo/myinfo'
import NotFound from '@/page/none'
import Audit from '@/page/audit/audit'
import Vul_audit from '@/page/audit/vul_aduit'
import Mytips from '@/page/personinfo/mytips'
import Myfocus from '@/page/personinfo/myfocus'
import Room1 from '@/page/room1/room1'
import Environment1 from '@/page/room1/environment'
import Energy1 from '@/page/room1/energy'
import Person1 from '@/page/room1/person'
import Comfortable1 from '@/page/room1/comfortable'
import Environment2 from '@/page/room2/environment'
import Energy2 from '@/page/room2/energy'
import Person2 from '@/page/room2/person'
import Comfortable2 from '@/page/room2/comfortable'
import Environment3 from '@/page/room3/environment'
import Energy3 from '@/page/room3/energy'
import Person3 from '@/page/room3/person'
import Comfortable3 from '@/page/room3/comfortable'
import Environment4 from '@/page/room4/environment'
import Energy4 from '@/page/room4/energy'
import Person4 from '@/page/room4/person'
import Comfortable4 from '@/page/room4/comfortable'
import Environment5 from '@/page/room5/environment'
import Energy5 from '@/page/room5/energy'
import Person5 from '@/page/room5/person'
import Comfortable5 from '@/page/room5/comfortable'
import Environment6 from '@/page/room6/environment'
import Energy6 from '@/page/room6/energy'
import Person6 from '@/page/room6/person'
import Comfortable6 from '@/page/room6/comfortable'

Vue.use(Router)

export default new Router({
  //mode:'history',
  routes: [{
      path: '/login',
      component: Login,
      name: '',
      hidden: true,
    },
    {
      path: '/404',
      component: NotFound,
      nam: '404',
      hidden: true,
    },
    {
      path: '/myfocus',
      component: Myfocus,
      name: 'table',
      hidden: true
    },
    {
      path: '/',
      component: Layout,
      name: 'HOME',
      children: [{
        path: '/home',
        component: Home,
        name: '环境与能耗感知',
      },
      {
        path: '/identification',
        component: Identification,
        name: '人员识别与信息感知',
      },
      {
        path: '/positioning',
        component: Positioning,
        name: '人员定位',
      },
      {
        path: '/evacuation',
        component: Evacuation,
        name: '人员疏散',
      },
      {
        path: '/iron',
        component: Iron,
        name: '钢铁公司',
      },
      {
        path: '/Henergy',
        component: HEnergy,
        name: '氢能能源',
      },
      {
        path: '/carbuilding',
        component: Carbuilding,
        name: '楼车协调',
      },
      {
        path: '/fivefloor',
        component: Fivefloor,
        name: '五层传感信息',
      },
      {
        path: '/mainpage',
        component: Mainpage,
        name: '全局调度',
      },
      {
        path: '/efficiency',
        component: Efficiency,
        name: '效益',
      }],
    },
    {
      path: '/',
      component: Layout,
      name: '1',
      hidden: true,
      leaf: true,
      children: [{
        path: '/myfocus',
        component: Mytips,
        name: '1',
      }],
    },
    {
      path: '/room1',
      component: Layout_room1,
      name: 'room1',
      hidden: false,
      brunch1: true,
      children: [
        { path: '/room1/environment', component: Environment1, name: '环境' },
        { path: '/room1/energy', component: Energy1, name: '能源' },
        // { path: '/room1/person', component: Person1, name: '人员' },
        { path: '/room1/comfortable', component: Comfortable1, name: '舒适度' },
      ]
    },
    {
      path: '/room2',
      component: Layout_room2,
      name: 'room2',
      hidden: false,
      children: [
        { path: '/room2/environment', component: Environment2, name: '环境' },
        { path: '/room2/energy', component: Energy2, name: '能源' },
        // { path: '/room2/person', component: Person2, name: '人员' },
        { path: '/room2/comfortable', component: Comfortable2, name: '舒适度' },
      ]
    },
    {
      path: '/room3',
      component: Layout_room3,
      name: 'room3',
      hidden: false,
      children: [
        { path: '/room3/environment', component: Environment3, name: '环境' },
        { path: '/room3/energy', component: Energy3, name: '能源' },
        // { path: '/room3/person', component: Person3, name: '人员' },
        { path: '/room3/comfortable', component: Comfortable3, name: '舒适度' },
      ]
    },
    {
      path: '/room4',
      component: Layout_room4,
      name: 'room4',
      hidden: false,
      children: [
        { path: '/room4/environment', component: Environment4, name: '环境' },
        { path: '/room4/energy', component: Energy4, name: '能源' },
        // { path: '/room4/person', component: Person4, name: '人员' },
        { path: '/room4/comfortable', component: Comfortable4, name: '舒适度' },
      ]
    },
    {
      path: '/room5',
      component: Layout_room5,
      name: 'room5',
      hidden: false,
      children: [
        { path: '/room5/environment', component: Environment5, name: '环境' },
        { path: '/room5/energy', component: Energy5, name: '能源' },
        // { path: '/room5/person', component: Person5, name: '人员' },
        { path: '/room5/comfortable', component: Comfortable5, name: '舒适度' },
      ]
    },
    {
      path: '/room6',
      component: Layout_room6,
      name: 'room6',
      hidden: false,
      children: [
        { path: '/room6/environment', component: Environment6, name: '环境' },
        { path: '/room6/energy', component: Energy6, name: '能源' },
        // { path: '/room6/person', component: Person6, name: '人员' },
        { path: '/room6/comfortable', component: Comfortable6, name: '舒适度' },
      ]
    },
    {
      path: '*',
      hidden: true,
      redirect: { path: '/404' }
    },
  ]
})

export const asyncRouterMap = [{
    path: '/',
    component: Layout,
    name: '账户管理',
    leaf: true,
    children: [{
      path: '/account',
      component: Account,
      name: '账号管理',
      meta: {
        permission: []
      }
    }]
  },
  {
    path: '/vuldetail',
    component: Vul_detail,
    name: 'vuldetail',
    hidden: true,
    meta: {
      permission: []
    }
  },
  {
    path: '/vuladuit',
    component: Vul_audit,
    name: 'vuladuit',
    hidden: true,
    meta: {
      permission: []
    }
  },
  {
    path: '/',
    component: Layout,
    name: '漏洞审核',
    leaf: true,
    children: [{
      path: '/audit',
      component: Audit,
      name: '漏洞审核',
      meta: {
        permission: []
      }
    }]
  },
  {
    path: '/vulshow',
    component: Vul_show,
    name: '漏洞展示',
    hidden: true,
    meta: {
      permission: []
    }
  },
]



