ul,
li,
input {
  position: fixed;
  margin: 0;
  padding: 0;
}
h2 {
  margin: 2%;
  font-size: 30px;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
/* 浮动 */
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  *zoom: 1;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  visibility: hidden;
  clear: both;
}
body {
  width: 100%;
  height: 100%;
  min-width: 1583px;
  /* 	height: 1080px;*/
  /* background-color: #000c3b; */
  background: url("../../assets/picture/bg.png");
  position: fixed;
}
.top {
  width: 99%;
  height: 100px;
  margin: 0 auto;
  color: skyblue;
  background-image: url(../../assets/iron/title.png);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0px 0;
  text-align: center;
}
/*  */
.left,
.right {
  width: 30%;
  height: 92%;
  margin-top: -1%;
  /* border: 2px solid #032d60; */
  border: 2px solid #032d60;
  box-shadow: inset 0 0 30px #07417a;
  background-image: radial-gradient(
    250px 250px at 50% 50%,
    hsla(209, 99%, 32%, 28),
    hsla(228, 100%, 0%, 0)
  );
}
.middle {
  width: 39.5%;
  height: 92%;
  margin-top: -0.5%;
  color: #83c7e3;
  /* border: 1px solid white; */
  text-align: center;
}
.header {
  text-align: center;
  color: #83c7e3;
}
.pic {
  width: auto;
  height: 50%;
  /* border:1px solid red; */
  color: #ffffff;
  text-align: center;
}
.middle p {
  margin-top: 3px;
}
#box1 {
  width: 80%;
  margin: 5% auto;
  text-align: center;
  color: #e7eaed;
  border: 2px solid skyblue;
  border-radius: 40px;
  padding-bottom: 6%;
}
#box2 {
  text-align: center;
  margin: 20px auto;
  width: 100%;
  height: 20%;
  color: #e7eaed;
  /* font-size: 50px; */
}
.system {
  width: auto;
  height: 52%;
  /* border: 1px solid whitesmoke; */
  background-image: url(../../assets/building&car/system.png);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0px -4px;
  margin-top: 8%;
}
table {
  width: 96%;
  height: 100%;
  margin: 2% auto;
  border-color: #87ceeb;
  font-size: 20px;
}
</style>


