<template>
  <section>
    <!--操作-->
    <!--button bar-->
    <el-col :span="24" class="toolbar" style="text-align:right;margin-bottom:0px;">
      <el-button type="primary" size="small" @click="toaddvul">新建</el-button>
      <el-button type="primary" @click="toexport" size="small">批量导出漏洞</el-button>
    </el-col>
    <!--列表-->
    <el-col :span="24" class="toolbar">
      <el-table ref="multipleTable" :data="tableData" style="width:100%;line-height:10px;" border stripe size="small" highlight-current-row @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="50" prop="select" v-if="select"></el-table-column>
        <el-table-column prop="vul_id" label="漏洞ID" width="150">
        </el-table-column>
        <el-table-column prop="find_date" label="发现时间" sortable width="150">
        </el-table-column>
        <el-table-column prop="vul_name" label="漏洞名称">
          <template slot-scope="scope">
            <img src="../../assets/img/icon/close_system3.gif" v-if="scope.row.severity==1" title="高危">
            <img src="../../assets/img/icon/close_system2.gif" v-if="scope.row.severity==2" title="中危">
            <img src="../../assets/img/icon/close_system1.gif" v-if="scope.row.severity==3" title="低危">
            <el-link :underline="false" @click="getid(scope.row.id)">{{scope.row.vul_name}}</el-link>
          </template>
        </el-table-column>
        <el-table-column prop="vul_state" label="漏洞状态" width="150">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="修复建议" placement="top">
              <i class="el-icon-chat-line-square" v-if="scope.row.solution!=''"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="修复建议" placement="top">
              <i class="el-icon-chat-line-square" v-if="scope.row.solution==''" style="color:#ddd" :class="text-mix"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="CVE" placement="top">
              <i class="el-icon-tickets" v-if="scope.row.cve_id!=''"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="CVE" placement="top">
              <i class="el-icon-tickets" v-if="scope.row.cve_id==''" style="color:#ddd"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="WAF" placement="top">
              <i class="el-icon-cpu" v-if="scope.row.wafdefend==1"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="WAF" placement="top">
              <i class="el-icon-cpu" style="color:#ddd" v-if="scope.row.wafdefend==0"></i>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column prop="popularity_num" label="人气" sortable width="120">
        </el-table-column>
        <el-table-column prop="comment_num" label="评论数" width="120">
        </el-table-column>
        <el-table-column prop="operator" label="操作" v-if="operator" width="120">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="编辑" placement="top">
              <i class="el-icon-edit-outline" @click="editid(scope.row.id)"></i>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top">
              <i class="el-icon-delete" @click="todelete(scope.row.id)"></i>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="24" class="toolbar" style="padding:10px 40px;">
      <el-pagination layout="total, prev, pager, next ,sizes ,jumper" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-size="20" :page-sizes="[10,20, 50, 100]" :total="total" style="float:left;">
      </el-pagination>
    </el-col>
  </section>
</template>
<script>
import MiddleWare from '../../common/js/middleware';
import { getvulslist, batchexport, deletevul } from '../../api/api'
export default {
  data() {
    return {
      select: true,
      operator: true,
      tableData: [],
      total: 0,
      page: 1,
      pagenum: 20,
      ids: []
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.page = val;
      this.getVuls();
    },
    handleSizeChange(val) {
      this.pagenum = val;
      this.getVuls();
    },
    handleSelectionChange(val) {
      for (let i = 0; i < val.length; i++) {
        this.ids.push(val[i].id)
      }
      this.ids = this.ids.filter(function(item, index, self) {
        return self.indexOf(item) == index;
      })
    },
    getid: function(id) {
      this.$router.push({
        path: '/vuldetail',
        name: 'vuldetail',
        query: {
          id: id,
          pgstatus: true
        }
      })
    },
    editid: function(id) {
      this.$router.push({
        path: '/vuldetail',
        name: 'vuldetail',
        query: {
          id: id,
          pgstatus: false
        }
      })
    },
    getVuls(obj) {
      if (!obj) {
        let obj = {}
        obj.page = this.page
        obj.pagenum = this.pagenum
        getvulslist(obj).then((res) => {
          let { status, message, result } = res.data;
          if (status == 2000) {
            this.total = result.total
            this.tableData = result.objects
          } else {
            this.total = 0
          }
        });
      } else {
        obj.page = this.page
        obj.pagenum = this.pagenum
        getvulslist(obj).then((res) => {
          let { status, message, result } = res.data;
          if (status == 2000) {
            this.total = result.total
            this.tableData = result.objects
          } else {
            this.total = 0
          }
        });
      }
    },
    //删除
    todelete(id) {
      let params = {
        id: id
      }
      deletevul(params).then((res) => {
        let { status, message } = res.data;
        if (status == 2000) {
          this.getVuls();
        } else {
          console.log('delete default')
        }
      })
    },
    toaddvul() {
      this.$router.push('/addvul')
    },
    //批量导出
    toexport() {
      if (this.ids.length == 0) {
        let params = {
          ids: 'all'
        }
        batchexport(params).then(data => {
          console.log(data)
          let { status, message } = data;
          if (status == 2000) {
            console.log('export succcess')
            windos.open()
          } else {
            console.log('export default')
          }
        })
        this.ids = []
      } else {
        let params = {
          ids: this.ids.join(',')
        }
        batchexport(params).then(data => {
          console.log(data)
          let { status, message } = data;
          if (status == 2000) {
            console.log('export succcess')
          } else {
            console.log('export default')
          }
        })
        this.ids = []
      }
    }
  },
  created() {
    this.getVuls();
  },
  mounted() {
    MiddleWare.$on('datachild', data => {
      this.getVuls(data)
    })
  }
}

</script>
<style scoped>
.toolbar {
  padding: 10px 40px;
}

.el-table--mini,
.el-table--small,
.el-table__expand-icon {
  font-size: 14px;
}

.el-table {
  color: #333;
  font-size: 14px;
}

.el-table>>>.cell {
  text-align: center;
}
.el-table>>>img
{
  width:18px;
  height:18px;
  padding:0;
  margin:0;
  position:relative;
  top:4px;
}
[class*=" el-icon-"],
[class^=el-icon-] {
  font-size: 18px;
  color: #1abc9c;
  margin: 0px 2px;
  cursor: pointer;
}
</style>



