<template>
  <section>
    <el-col :span="24" class="toolbar" style="padding:20px 100px 20px 100px">
      <el-col :span="24" class="tableheader" style="text-align:left;padding:10px 0px;">
        <p v-if="total==0">暂无评论</p>
        <p v-else>全部评论({{total}})</p>
      </el-col>
      <el-col :span="24" class="comment" v-for="comment in comments" :key="comment.id">
        <!-- <el-col :span="4" style="margin-top:10px;">
          <img v-if="isAnonym=true" src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2814109303,2147705560&fm=27&gp=0.jpg" class="avatar">
          <img v-else src="comment.fromAvatar" class="avatar">
        </el-col> -->
        <el-col :span="18" style="text-align:left;padding:10px 40px 10px 20px;word-wrap: break-word;word-break: normal;">
          <el-col :span="2">
           <span v-if="comment.is_anonymous==true">匿名</span> 
           <span v-else>{{comment.username}}</span>
          </el-col>
          <el-col :span="10" >{{comment.create_time}}</el-col>
          <el-col :span="2" :offset="8">{{comment.id}}F</el-col>
          <p>{{comment.content}}</p>
             <span class="commenttools" @click="islike=!islike,likeClick()">
                <i class="el-icon-thumb"></i>
                <span>{{comment.likeNum}}</span>
             </span>
             <span class="commenttools" @click="isdislike=!isdislike,dislikeClick()">
                <i class="iel-icon-thumb"></i>
                <span>{{comment.dislikeNum}}</span>
             </span>
           <span class="commenttools">
            <el-link :underline="false" @click="dialogFormVisible='true'">回复</el-link>
            <el-dialog :title="dialogtitle" :visible.sync="dialogFormVisible" width="30%" style="text-align:left;">
              <textarea placeholder="请输入内容" v-model="newcomment.content">
              </textarea>
              <el-col style="text-align:left;margin-left:95px;">
                <input type="checkbox" v-model="newcomment.is_anonymous" id="r1"><label for="r1">匿名回复</label>
              </el-col>
              <div slot="footer" class="dialog-footer">
                <el-button type="primary" size="small" @click="submitComment('newcomment'),dialogFormVisible = false">确 定</el-button>
                <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
              </div>
            </el-dialog>
          </span>
          <div v-if="comment.comment_username=='null'"></div>
          <div v-else>
            <el-col :span="2">
             <span v-if="comment.comment_is_anonymous=true">匿名</span> 
             <span v-else>{{comment.comment_username}}</span>
            </el-col>
            <el-col :span="1" >{{comment.comment_time}}</el-col>
            <p>{{comment.comment_content}}</p>
          </div>
        </el-col>
      </el-col>
    </el-col>
  </section>
</template>
<script>
import {postfavour,addcomment} from '../../api/api';
import {getcomment} from '../../api/api';
export default {
  data() {
    return {
      newcomment:{
      /*username:'',//当前回复人
       comment_is_anonymous:'',//被回复人是否匿名
       comment_username:'',//被回复人
       comment_time:'',
       create_time:'',//
       like_num:'',
      oppose_num:'',
      comment_content:'',//被回复内容
      id:'',
      is_anonymous:'',
      content:''//当前回复
      }*/
      vul_id:this.$route.query.id,
      content:'',
      is_anonymous:'',
      comment_id:1
      },
      total:0,
      comments: [],
      islike:false,
      isdislike:false
    }
  },
  created() {
    let params={vul_id:this.$route.query.id}  
    getcomment(params).then(res => {
      this.comments  = res.data.result.comment;
      this.total = res.data.result.total;
    })
  },
  methods: {
    /*addcomment(){
      this.newcomment.username = comment.username,//当前回复人
      this.newcomment.comment_is_anonymous = comment.is_anonymous,//被回复人是否匿名
      this.newcomment.comment_username = username,//被回复人
      this.newcomment.comment_time = create_time,
      this.newcomment.create_time = new Date().toLocaleString(),//
      this.newcomment.like_num = 0,
      this.newcomment.oppose_num = 0,
      this.newcomment.comment_content = content,//被回复内容
      this.newcomment.id:null,
      this.newcomment.is_anonymous:'',
    },*/
    submitComment() {
      let params = this.newcomment;
       addcomment(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          let params={vul_id:this.$route.query.id}  
          getcomment(params).then(res => {
          this.comments  = res.data.result.comment;
          this.total = res.data.result.total;
          });
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
        },
    
    likeClick() {
    if(islike==true){
      let params = {
        obj_model:"vulcomment",
        obj_type:"up" ,
        obj_id:comment.id,
        user_id:comment.username
      };
       postfavour(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          console.log(result)
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      });
    }
    else{
      dislikeClick();
    }
      },
      dislikeClick() {
      if(isdislike==true)
      {
        let params = {
        obj_model:"vulcomment",
        obj_type:"down" ,
        obj_id:comment.id,
        user_id:comment.username
      };
       postfavour(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          console.log(result)
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      });
      }
      else{
        likeClick();
      }
    }
  }
}

</script>
<style>

</style>



