  requestenvironment6,
} from "../../api/api";
import mailTable from "@/components/table/tableVerticalroom";
export default {
  components: {
    mailTable,
  },
  data() {
    return {
      src: "time1.js",
      currTime: new Date(), //获取到的时间
      updateTimer: null, // 更新时钟的timer
      millisecond: false, //毫秒
      tableData: [25, 35, 1, 68, 52, 3, 3],
      tableKey: [
        "人数/个",
        "人均面积/(个/m2)",
        "房间职能",
        "温度/℃",
        "湿度/%",
        "PM2.5/(μg/m3)",
        "CO2/ppm",
      ],
      note: {
        backgroundImage: "url(" + require("../../assets/picture/bg.png") + ")",
        // marginTop: "5px",
      },
      box1: {
        backgroundImage:
          "url(" + require("../../assets/picture/title.png") + ")",
      },
      chartOptions1: {
        chart: {
          backgroundColor: "none",
          height: 255,
        },
        title: {
          text: "",
          style: {
            color: "white",
          },
        },
        yAxis: {
          title: {
            text: "房间人数",
            style: {
              color: "white",
            },
          },
          textStyle: {
            color: "white",
          },
          labels: {
            style: {
              color: "#fff",
              fontFamily: "微软雅黑",
            },
          },
        },
        xAxis: {
          labels: {
            style: {
              color: "#fff",
              fontFamily: "微软雅黑",
            },
          },
          categories: [
            "0:00",
            "1:00",
            "2:00",
            "3:00",
            "4:00",
            "5:00",
            "6:00",
            "7:00",
            "8:00",
            "9:00",
            "10:00",
            "11:00",
          ],
        },
        legend: {
          layout: "vertical",
          align: "right",
          verticalAlign: "middle",
          itemStyle: {
            color: "white",
          },
        },
        credits: {
          enabled: false,
        },

        series: [
          {
            name: "人数",
            data: [5, 6, 3, 10, 7, 7, 7, 8, 7, 9, 5, 5],
          },
        ],
        responsive: {
          rules: [
            {
              condition: {
                maxWidth: 500,
              },
              chartOptions: {
                legend: {
                  layout: "horizontal",
                  align: "center",
                  verticalAlign: "bottom",
                },
              },
            },
          ],
        },
      },
      chartOptions2: {
        chart: {
          type: "pie",
          height: 290,
          backgroundColor: "none",
          options3d: {
            enabled: true,
            alpha: 45,
            beta: 0,
          },
        },
        legend: {
          align: "right", //程度标的目标地位
          verticalAlign: "top", //垂直标的目标地位
          enabled: true,
        },
        credits: {
          enabled: false,
        },
        title: {
          text: "",
          style: {
            color: "white",
          },
        },
        tooltip: {
          pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>",
        },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: "pointer",
            depth: 35,
            dataLabels: {
              enabled: true,
              format: "{point.name}",
              style: {
                fontSize: 15,
              },
            },
          },
        },
        series: [
          {
            type: "pie",
            name: "访客种类占比",
            data: [
              ["楼内人员", 45.0],
              ["有访问记录的访客", 26.8],
              {
                name: "新访客",
                y: 28.2,
                sliced: true,
                selected: true,
              },
            ],
          },
        ],
      },
    };
  },
  mounted() {
    document.getElementById("real-time").value = this.timeDisplay();
  },
  methods: {
        timeDisplay() {
      // 时间展示区
      let years = this.currTime.getFullYear();
      let months = this.currTime.getMonth() + 1;
      let days = this.currTime.getDate();

      let hours =
        this.currTime.getHours() > 9
          ? this.currTime.getHours()
          : "0" + this.currTime.getHours();
      let minutes =
        this.currTime.getMinutes() > 9
          ? this.currTime.getMinutes()
          : "0" + this.currTime.getMinutes();
      let seconds =
        this.currTime.getSeconds() > 9
          ? this.currTime.getSeconds()
          : "0" + this.currTime.getSeconds();
      this.currTime =
        years +
        "/" +
        months +
        "/" +
        days +
        "  " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds;
      return (
        years +
        "/" +
        months +
        "/" +
        days +
        "  " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds
      );
    },
    myCallback1() {
      console.log("this is callback function");
    },
    myCallback2() {
      console.log("this is callback function");
    },
  },
  created() {
    this.currTime = new Date();
    clearInterval(this.updateTimer);
    this.updateTimer = setInterval(() => {
      this.currTime = new Date();
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.updateTimer);
  },
};
</script>

