<template>
  <section>
  <el-col :span="24" style="padding:0px 10px 10px 40px;">
      <el-table :data="tableData" stripe style="width:100%" row-style="height:50px" border size="small" highlight-current-row>
        <el-table-column prop="number" label="序号" width="160">
          <template slot-scope="scope">
            {{scope.$index+1}}
          </template>
        </el-table-column>
        <el-table-column prop="update_time" label="提交时间" width="160">
        </el-table-column>
        <el-table-column prop="vul_name" label="漏洞名称" width="350">
        </el-table-column>
        <el-table-column prop="open_field" label="运营字段" width="220">
        </el-table-column>
        <el-table-column prop="audit_status" label="审核状态">
          <template slot-scope="scope">
            <el-button type="info" v-if="scope.row.audit_status==0">等待审核</el-button>
            <el-button type="success" v-if="scope.row.audit_status==1" @click="dialogTableVisible = true,getresult(scope.row.audit_id)">审核通过</el-button>
              <el-dialog title="审核详情" :visible.sync="dialogTableVisible" width="30%" :before-close="handleClose">
               <span>漏洞名称：{{vul_name}}</span>
               <span>提交人：{{auditor}}</span>
               <span>提交时间：{{create_time}}</span>
               <span>提交内容：{{reason}}</span>
               <span>审核意见：审核通过</span>
               <span>评分：{{score}}</span>
               <span slot="footer" class="dialog-footer">
                 <el-button @click="dialogVisible = false">取 消</el-button>
                 <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
               </span>
              </el-dialog>
            <el-button type="danger" v-if="scope.row.audit_status==2">审核未通过</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
  </section>
</template>
<script>
import {getvullist,getauditresult} from '../../api/api'
export default {
  data() {
    return {
      number:0,
	    tableData:[],
      vul_name:'',
      auditor:'',
      create_time:'',
      reason:'',
      score:0,
      total:0,
      pagenum: 20,
      page: 1,
      dialogTableVisible: false
    }
  },
  created() {
    getvullist({}).then(res => {
      let { status, message, result } = res.data;
      if (status == 2000) {
          this.tableData = result.result;
          this.total = result.total;
      }
    });
    
  },
  methods: {
    getresult(param){
     let params={audit_id:param}  
     getauditresult(params).then(res => {
      this.vul_name  = res.data.result.vul_name;
      this.auditor = res.data.result.auditor;
      this.create_time = res.data.result.create_time;
      this.reason = res.data.result.reason;
      this.score = res.data.result.score;
    })
    }
  }
}

</script>
<style scoped>
.el-table {
  color: #333;
  font-size: 14px;
}

.el-table>>>.cell {
  text-align: center;
}

</style>



