  background-image: url(../../assets/iron/title.png);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0px 0;
  text-align: center;
  /* border:1px solid red */
}

/* 左侧盒子 */
.left,
.right {
  width: 29.2%;
  height: 100%;
  background-image: radial-gradient(
    250px 250px at 50% 50%,
    hsla(209, 99%, 32%, 28),
    hsla(228, 100%, 0%, 0)
  );
}
.middle {
  width: 41.6%;
  height: 69%;
  color: #83c7e3;
  /* background-image: url(../../assets/iron/title.png);
  background-repeat: no-repeat;
  background-size: 210%;
  background-position: -438px 8px; */
  text-align: center;
}
#system {
  width: 100%;
  height: 410px;
  margin-top: 2%;
  border: 1px solid #032d60;
  box-shadow: inset 0 0 30px #07417a;
  /* background: url(../../assets/iron/融合系统-1.png); */
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  padding-bottom: 3.9%;
}
.buttom {
  width: 100%;
  height: 30%;
  border: 2px solid #032d60;
  box-shadow: inset 0 0 30px #07417a;
  margin: 0 0 0 0;
}
.pic,
.pic2 {
  width: 100%;
  height: 254px;
  border: 1px solid #032d60;
  box-shadow: inset 0 0 30px #07417a;
}
.data {
  /* 左右表格的行高 */
  height: 157px;
}
#dt1,
#dt2 {
  height: 160px;
  margin: 0px auto;
  color: #ffffff;
  text-align: center;
  display: table;
  border-collapse: separate;
  box-sizing: border-box;
  border-spacing: 1px;
  border-color: #032d60;
  box-shadow: inset 0 0 30px #07417a;
  /* background-image: radial-gradient(250px 250px at 50% 50%,hsla(209,99%,32%,28),hsla(228,100%,0%,0)); */
}
.gas {
  width: 33.3%;
  height: 276px;
  padding-top: 1%;
  /* 	border: 1px solid orange; */
}
#dt3 {
  height: 200px;
  margin: 0px auto;
  text-align: center;
  border-spacing: 1px;
  border-color: #032d60;
  box-shadow: inset 0 0 30px #07417a;
  color: #ffffff;
}
#time {
  margin-top: -10px;
  width: auto;
  height: 20px;
  color: #ffffff;
  font-size: 24px;
}
</style>



