<template>
  <highcharts :options="chartOptions" :callback="myCallback"></highcharts>
</template>
<script>
export default {
  data() {
    return {
      chartOptions: {
        chart: {
          type: "pie",
          backgroundColor: "none",
          height: 500,
         width:600,
          options3d: {
            enabled: true,
            alpha: 45,
            beta: 0
          }
        },
        title: {
          text: ""
        },
        tooltip: {
          pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>"
        },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: "pointer",
            depth: 35,
            dataLabels: {
              style: {
                fontSize: "20px",
                color: "#fff",
                fontFamily: "微软雅黑"
              },
              enabled: true,
              // format: "{point.name}"
              formatter: function() {

                return (
                  // '<span ' +
                  // // this.point.color +
                  // '"> ' +
                  this.point.name+
                  // this.y +
                  "，" +
                  this.percentage +
                  "%"
                  
                );
              }
            }
          }
        },
        credits: {
          enabled: false
        },
        series: [
          {
            type: "pie",
            name: "能耗占比",
            data: [
              ["空调", 45.0],
              ["实验设备", 26.8],
              {
                name: "照明",
                y: 12.8,
                sliced: true,
                selected: true
              },
              ["xx", 8.5],
              ["xxx", 6.2],
              ["其他", 0.7]
            ]
          }
        ]
      }
    };
  },
  mounted() {},
  methods: {
    myCallback() {
      console.log("this is callback function");
    }
  }
};
</script>
<style>
.highcharts-container {
  width: 100%;
  height: 90%;
  border: 0px solid #ddd;
  margin-bottom: 10%
}
</style>


