<template>
  <section>
    <el-col :span="24" style="margin-top:10px">
      <el-tabs type="border-card">
        <el-tab-pane>
          <span slot="label">积分获得列表</span>
          <Littlenotice></Littlenotice>
         <getpoints ></getpoints>
        </el-tab-pane>
        <el-tab-pane>
          <span slot="label">积分消费列表</span>
          <Littlenotice></Littlenotice>
          <pointscost></pointscost>
        </el-tab-pane>
        <el-tab-pane>
          <span slot="label">积分规则简介</span>
          <Littlenotice></Littlenotice>
          <pointsrule></pointsrule>
        </el-tab-pane>
      </el-tabs> 
    </el-col>
  </section>
</template>
<script>
import basicinfo from '@/components/personinfoview/basic_info';
import msglist from '@/components/personinfoview/msg_list';
import getpoints from '@/components/personinfoview/points_get';
import pointscost from '@/components/personinfoview/points_cost';
import pointsrule from '@/components/personinfoview/points_rule';
import Littlenotice from '@/components/personinfoview/littlenotice';
export default {
  data(){
    return{
      points:0,
    }
  },
 methods: {
    routeget(){
      this.$router.push('/mytips/getpoints');
    },
    routecost(){
      this.$router.push('/mytips/pointscost');
    },
    routerule(){
      this.$router.push('/mytips/pointsrule');
    }
  }, 
  components: {
    basicinfo,
    msglist,
    pointscost,
    getpoints,
    pointsrule,
    Littlenotice
  }
}

</script>



