<template>
  <section>
    <el-col :span="8" style="margin-left:40px;padding:10px;border:1px dashed #C0C4CC">
      <el-form ref="form" :model="form" label-width="100px" @submit.prevent="onSubmit" style="text-align:left" size="mini">
        <el-form-item label="账号名称">
          <el-input v-model="form.username" @keyup.enter.native="search"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="form.email" @keyup.enter.native="search"></el-input>
        </el-form-item>
        <el-form-item label="手机">
          <el-input v-model="form.phone" @keyup.enter.native="search"></el-input>
        </el-form-item>
        <el-form-item label="所属分组">
          <el-input v-model="form.group_name" @keyup.enter.native="search"></el-input>
        </el-form-item>
        <el-form-item style="margin-top:20px;">
          <el-button type="primary" size="small" @click="search">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-col :span="24" style="padding:20px 10px 10px 40px;text-align:left;">
      <el-button type="primary" @click="toaddaccount(),dialogFormVisible = true" size="small">添加账号</el-button>
    </el-col>
    <el-dialog :title="dialogtitle" :visible.sync="dialogFormVisible" width="30%" style="text-align:left;">
      <el-form :model="addacc" size="mini">
        <el-form-item label="账号名" :label-width="formLabelWidth" v-if="this.dialogtitle=='新建账号'">
          <el-input v-model="addacc.username" autocomplete="off" placeholder="填写组名称" @keyup.enter.native="toautoappend()"></el-input>
        </el-form-item>
        <el-form-item label="账号名" :label-width="formLabelWidth" v-if="this.dialogtitle=='编辑账号'">
          <el-input v-model="addacc.username" autocomplete="off" placeholder="填写组名称" disabled></el-input>
        </el-form-item>
        <el-form-item label="姓名" :label-width="formLabelWidth">
          <el-input v-model="addacc.chinese_name" autocomplete="off" placeholder="填写组名称"></el-input>
        </el-form-item>
        <el-form-item label="邮箱地址" :label-width="formLabelWidth">
          <el-input v-model="addacc.email" autocomplete="off" placeholder="填写组名称"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" :label-width="formLabelWidth">
          <el-input v-model="addacc.phone" autocomplete="off" placeholder="填写组名称"></el-input>
        </el-form-item>
        <el-form-item label="账号组" :label-width="formLabelWidth">
          <el-select v-model="addacc.group_uuid" placeholder="请选择数据来源">
            <el-option v-for="(item,index) in grouplist" :value="item.group_uuid" :key="index" :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门" :label-width="formLabelWidth">
          <el-input v-model="addacc.department" autocomplete="off" placeholder="填写组名称"></el-input>
        </el-form-item>
        <el-form-item label="职位" :label-width="formLabelWidth">
          <el-input v-model="addacc.position" autocomplete="off" placeholder="填写组名称"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer" v-if="this.dialogtitle=='新建账号'">
        <el-button type="primary" size="small" @click="submitForm('addacc','add'),dialogFormVisible = false">确 定</el-button>
        <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
      </div>
      <div slot="footer" class="dialog-footer" v-if="this.dialogtitle=='编辑账号'">
        <el-button type="primary" size="small" @click="submitForm('addacc','edit',addacc.account_id),dialogFormVisible = false">确 定</el-button>
        <el-button @click="dialogFormVisible = false" size="small">取 消</el-button>
      </div>
    </el-dialog>
    <el-col :span="24" style="padding:0px 10px 10px 40px;text-align:left">
      <el-table :data="tableData" stripe style="width:100%" border size="mini">
        <el-table-column prop="username" label="账号名">
        </el-table-column>
        <el-table-column prop="email" label="邮箱地址" width="250">
        </el-table-column>
        <el-table-column prop="phone" label="手机号">
        </el-table-column>
        <el-table-column prop="group_name" label="所属分组">
        </el-table-column>
        <el-table-column prop="creator" label="创建者">
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间" width="180">
        </el-table-column>
        <el-table-column prop="operator" label="操作">
          <template slot-scope="scope">
            <el-link :underline="false" @click="editaccount(scope.row),dialogFormVisible=true">编辑账号</el-link>
            <el-link :underline="false" @click="todeleteaccount(scope.row.account_id)">删除</el-link>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="24" style="padding:20px 10px 10px 40px;text-align:left;">
      <el-pagination layout="total, prev, pager, next ,sizes ,jumper" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-size="20" :page-sizes="[10,20, 50, 100]" :total="total" style="float:left;">
      </el-pagination>
    </el-col>
  </section>
</template>
<script>
import MiddleWare from '../../common/js/middleware'
import { getaccoutlist, deleteaccount, accforminit, addaccount, autoappend } from '../../api/api'
export default {
  props: {
    gname: String
  },
  data() {
    return {
      total:0,
      form: {
        username: '',
        email: '',
        phone: '',
        group_name: '',
      },
      gname: this.gname,
      para: {},
      grouplist: [],
      tableData: [],
      dialogFormVisible: false,
      dialogtitle: '',
      addacc: {
        username: '',
        chinese_name: '',
        email: '',
        phone: '',
        group_uuid: '',
        department: '',
        position: '',
      },
      formLabelWidth: '80px',
      pagenum: 20,
      page: 1,
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.page = val;
      this.getacclist()
    },
    handleSizeChange(val) {
      this.pagenum = val;
      this.getacclist()
    },
    getacclist(obj) {
      let params = {
        page: this.page,
        pagenum: this.pagenum
      }
      Object.assign(params, obj)
      getaccoutlist(params).then((res) => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.total = result.total
          this.tableData = result.result
        } else {
          this.total = 0
        }
      })
    },
    submitForm(formName, action, account_id) {
      let params = {
        username: this.addacc.username,
        chinese_name: this.addacc.chinese_name,
        email: this.addacc.email,
        department: this.addacc.department,
        phone: this.addacc.phone,
        department: this.addacc.department,
        group_uuid: this.addacc.group_uuid
      };
      params.action = action,
        params.account_id = account_id
      addaccount(params).then(data => {
        let { status, message } = data;
        if (status == 2000) {
          this.getacclist();
        } else {
          this.$message({
            showClose: true,
            message: message,
            type: 'warning'
          })
        }
      })
    },
    toaddaccount() {
      this.dialogtitle = '新建账号'
      this.addacc = {
        username: '',
        chinese_name: '',
        email: '',
        phone: '',
        group_uuid: '',
        department: '',
        position: '',
      }
    },
    editaccount(obj) {
      this.dialogtitle = '编辑账号'
      this.addacc = obj
    },
    toautoappend() {
      let params = {
        username: this.addacc.username
      }
      autoappend(params).then((res) => {
        let { status, message, result } = res.data;
        if (status == 2000) {
          this.addacc = result
        } else {
          console.log('default')
        }
      })
    },
    search: function() {
      for (let i in this.form) {
        if (this.form[i] != '') {
          this.para[i] = this.form[i]
        }
        console.log(this.para)
      }
      this.getacclist(this.para)
    },
    todeleteaccount(id) {
      let params = {
        account_id: id
      }
      deleteaccount(params).then(data => {
        let { status, message, result } = data;
        if (status == 2000) {
          this.getacclist()
        } else {
          console.log('default')
        }
      })
    }
  },
  created() {
    this.getacclist();
    accforminit().then((res) => {
      let { status, message, result } = res.data;
      if (status == 2000) {
        this.grouplist = result
      } else {
        console.log('default')
      }
    })
  },
  mounted() {
    MiddleWare.$on('postgroup', data => {
      let obj={
        group_name:data
      }
      this.getacclist(obj)
    })
  }
}

</script>
<style scoped>
.el-table {
  color: #333;
  font-size: 14px;
}

.el-table>>>.cell {
  text-align: center;
}

</style>



