<template>
  <el-container style="height:100%">
    <el-header style="padding:0px;">
      <headertop></headertop>
    </el-header>
    <el-container>
      <el-main>
        <section>
          <!--search bar-->
          <el-col :span="24" class="toolbar">
            <searchform></searchform>
          </el-col>
          <!--漏洞详情-->
          <tableview></tableview>
        </section>
      </el-main>
    </el-container>
    <el-footer style="padding:0px;">
      <footerbottom></footerbottom>
    </el-footer>
  </el-container>
</template>
<script>

import tableview from '@/components/table/tableshow';
import searchform from '@/components/search/search_form';
import headertop from '@/components/common/header';
import footerbottom from '@/components/common/footer'
export default {
  components: {
    tableview,
    searchform,
    headertop,
    footerbottom
  },
  methods: {
    totalexport() {
      this.$confirm('此操作将导出漏洞, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '失败'
        });
      });
    }
  },
}

</script>
<style scoped>
.moresearch:focus,
.moresearch:hover {
  color: #1abc9c;
  border: 1px solid #1abc9c;
  background: transparent;
}
</style>



