      },
      box2: {
        backgroundImage:
          "url(" + require("../../assets/img/amiddboxttop.png") + ")",
      },
    };
  },
  mounted() {
    var video = document.getElementById("myvideo");

    var vList = [
      "http://img.yopoo.cn/banner_video.mp4",
      "http://img.yopoo.cn/banner_video.mp4",
      "http://img.yopoo.cn/banner_video.mp4",
      "http://img.yopoo.cn/banner_video.mp4",
    ]; // 初始化播放列表，这里的url要用相对路径
    var vLen = vList.length;
    var curr = 0;
    $(document).ready(function () {
      play();

      video.addEventListener("ended", function () {
        play();
      });
    });

    function play() {
      video.src = vList[curr];
      video.load();
      video.play();
      curr++;
      if (curr >= vLen) {
        curr = 0; //重新循环播放
      }
    }
  },
  methods: {
    next() {
      this.$router.push("/evacuation");
    },
    before() {
      this.$router.push("/identification");
    },
    home() {
