        container: document.getElementById('humimap3')
      });
      var points3 = [];
      var max3 = 20;
      var min3 = 0;
      var val3 =[];
      for (var j = 0; j < 4; j++){
        for (var i = 0; i < 5; i++){
          var temp = Math.random()*20
          // var temp = 20
          var point = {
            x: 50 + i*50,
            y: 60 + j*60,
            value: temp,
            radius:100,
          }
          points3.push(point)
        }
      }
      var data3 = {
        max: max3,
        data: points3
      }
