                fontSize: "18px",
              },
            },

            stackLabels: {
              // 堆叠数据标签
              enabled: false,
              style: {
                fontWeight: "bold",
                // color: (Highcharts.theme && Highcharts.theme.textColor) || 'gray'
              },
            },
          },
          legend: {
            x: 20,
            itemStyle: {
              color: "white",
              fontSize: 20,
            },
            itemHoverStyle: {
              color: "yellow",
            },
          },
          // tooltip: {
          //   formatter: function () {
          //     return (
          //       "<b>" +
          //       this.x +
          //       "</b><br/>" +
          //       this.series.name +
          //       ": " +
          //       this.y +
          //       "<br/>" +
          //       "总量: " +
          //       this.point.percentage:.1f

          //     );
          //   },
          // },
          tooltip: {
            pointFormat: "{series.name}: <b>{this.percentage:.1f}%</b>"
          },
          plotOptions: {
            area: {
              stacking: "normal",
              dataLabels: {
                enabled: true,
                // color: (Highcharts.theme && Highcharts.theme.dataLabelsColor) || 'white',
                style: {
                  // 如果不需要数据标签阴影，可以将 textOutline 设置为 'none'
                  textOutline: "none",
                  fontSize: 20,
                },
              },
            },
            pie: {
              allowPointSelect: true,
              cursor: "pointer",
              depth: 35,
              dataLabels: {
                enabled: true,
                // format: "{point.name}",
                formatter: function () {
                  return this.point.name + ',' + this.percentage + '%';
                },
                style: { fontSize: "18px", color: "white" },
              },
            },
          },
          series: [
            {
              name: "Brands",
              colorByPoint: true,
              data: [
                {
                  name: "空调",
                  y: 61.41,
                  sliced: true,
                  dataLabels: {
                    // connectorColor: 'red', // 连接线颜色，默认是扇区颜色
                    distance: 30, // 数据标签与扇区距离
                    connectorPadding: 20, // 数据标签与连接线的距离
                    connectorWidth: 3,
                  },
                },
                {
                  name: "照明",
                  y: 11.84,
                  color: "yellow",
                  dataLabels: {
                    // connectorColor: 'red', // 连接线颜色，默认是扇区颜色
                    distance: 30, // 数据标签与扇区距离
                    connectorPadding: 20, // 数据标签与连接线的距离
                    connectorWidth: 3,
                  },
                },
                {
                  name: "服务器",
                  y: 10.85,
                  dataLabels: {
                    // connectorColor: 'red', // 连接线颜色，默认是扇区颜色
                    distance: 30, // 数据标签与扇区距离
                    connectorPadding: 20, // 数据标签与连接线的距离
                    connectorWidth: 3,
                  },
                },
                {
                  name: "其他",
                  y: 2.61,
                  dataLabels: {
                    // connectorColor: 'red', // 连接线颜色，默认是扇区颜色
                    distance: 30, // 数据标签与扇区距离
                    connectorPadding: 20, // 数据标签与连接线的距离
                    connectorWidth: 3,
                  },
                },
              ],
            },
          ],
        },

        computed: {},
        methods: {
          environment() {
            this.$router.push("/room1/environment");
          },
          energy() {
            this.$router.push("/room1/energy");
          },
          person() {
            this.$router.push("/room1/person");
          },
          comfortable() {
            this.$router.push("/room1/comfortable");
          },
          // 导出excle
          exportToExcel() {
            let et = XLSX.utils.table_to_book(
              document.getElementById("consumption")
            ); //此处传入table的DOM节点
            let etout = XLSX.write(et, {
              bookType: "xlsx",
              bookSST: true,
              type: "array",
            });
            try {
              FileSaver.saveAs(
                new Blob([etout], {
                  type: "application/octet-stream",
                }),
                "consumption_data.xlsx"
              ); //trade-publish.xlsx 为导出的文件名
            } catch (e) {
              console.log(e, etout);
            }
            return etout;
          },
          //打印页面内容
          printContent() {
            let wpt = document.getElementById("consumption");
            let newContent = wpt.innerHTML;
            let oldContent = document.body.innerHTML;

            document.body.innerHTML = newContent;
            window.print(); //打印方法
            window.localtion.reload();
            document.body.innerHTML = oldContent;
          },
          // 选择按钮
          toggleSelection(rows) {
            if (rows) {
              rows.forEach((row) => {
                this.$refs.multipleTable.toggleRowSelection(row);
              });
            } else {
              this.$refs.multipleTable.clearSelection();
            }
          },
          handleSelectionChange(val) {
            this.multipleSelection = val;
          },
        },
        components: {
          basicinfo,
          vullist,
        },
      };
    },
    mounted() {
      // this.myEcharts();]
      // document.getElementById('real-time').value = this.currentTime
      document.getElementById("real-time").value = this.timeDisplay();
      // clearInterval(this.updateTimer)
      // this._cache_ = setInterval(this.currTime, 1000);
    },
    created() {
      this.currTime = new Date();
      clearInterval(this.updateTimer);
      this.updateTimer = setInterval(() => {
        this.currTime = new Date();
      }, 1000);
    },
    computed: {},
    methods: {
      timeDisplay() {
        // 时间展示区
        let years = this.currTime.getFullYear();
        let months = this.currTime.getMonth() + 1;
        let days = this.currTime.getDate();

        let hours =
          this.currTime.getHours() > 9
            ? this.currTime.getHours()
            : "0" + this.currTime.getHours();
        let minutes =
          this.currTime.getMinutes() > 9
            ? this.currTime.getMinutes()
            : "0" + this.currTime.getMinutes();
        let seconds =
          this.currTime.getSeconds() > 9
            ? this.currTime.getSeconds()
            : "0" + this.currTime.getSeconds();
        this.currTime =
          years +
          "/" +
          months +
          "/" +
          days +
          "  " +
          hours +
          ":" +
          minutes +
          ":" +
          seconds;
        return (
          years +
          "/" +
          months +
          "/" +
          days +
          "  " +
          hours +
          ":" +
          minutes +
          ":" +
          seconds
        );
      },
    },

  };
</script>

<style scoped>
  body,
  p,
  ul,
  li,
  input {
    margin: 0;
    padding: 0;
  }

  .real-data,
  .real-data input {
    background-color: #1c2833 !important;
    /* margin-left: 2.5%; */
    /* margin-right: 2.5%; */
    border-left: 1px;
    border-top: 1px;
    border-bottom: none;
  }

  .real-data {
    border-radius: 6px;
    margin-bottom: 0px;
    margin-left: 5%;
    font-weight: 600;
    padding-right: 0%;
    width: 90%;
  }

  #maincontent {
    margin-top: 12px;
    width: 81%;
  }

  li {
    list-style: none;
  }

  a {
    text-decoration: none;
  }

  /* 浮动 */
  .pull-left {
    float: left;
  }

  .pull-right {
    float: right;
  }

  .clearfix {
    *zoom: 1;
  }

  .clearfix:after {
    content: ".";
    display: block;
    height: 0;
    visibility: hidden;
    clear: both;
  }

  body {
    width: 100%;
    height: 1715px;
    background-color: #344a5d;
  }

  /* 总高度的1/3*/

  .trend {
    width: 40%;
    height: 550px;
    border: 1px solid #2c58a6;
    box-shadow: 0px 0px 6px #2c58a6;
    margin: 2% 0% 2% 3%;
    position: relative;
  }

  .elec {
    width: 39%;
    height: 550px;
    border: 1px solid #2c58a6;
    box-shadow: 0px 0px 6px #2c58a6;
    margin: 2% 2% 2% 1%;
    position: relative;
  }

  .info {
    width: 37%;
    height: 140px;
    border: 1px solid #344a5d;
    border-radius: 20px;
    background-color: #344a5d;
    margin-left: 27.5%;
  }

  .equip {
    width: 36%;
    height: 550px;
    margin-top: 2%;
    border: 1px solid #2c58a6;
    box-shadow: 0px 0px 6px #2c58a6;
    margin: 2% 2% 2% 0%;
    margin-left: 10px;
    position: relative;
  }

  .dataTable {
    margin-top: 0%;
    width: 40%;
    height: 500px;
    border: 1px solid white;
    border-radius: 20px;
    background-color: white;
    margin-left: 26%;
  }

  .form-control {
    width: 60%;
    height: 38px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out 0.15s,
      -webkit-box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }

  .l_t_line {
    width: 5px;
    height: 24px;
    left: -3px;
    top: -3px;
  }

  .t_l_line {
    height: 5px;
    width: 26px;
    left: -3px;
    top: -3px;
  }

  .t_line_box {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .t_line_box i {
    background-color: #4788fb;
    box-shadow: 0px 0px 10px #4788fb;
    position: absolute;
  }

  .t_r_line {
    height: 5px;
    width: 26px;
    right: -3px;
    top: -3px;
  }

  .r_t_line {
    width: 5px;
    height: 24px;
    right: -3px;
    top: -3px;
  }

  .l_b_line {
    width: 5px;
    height: 24px;
    left: -3px;
    bottom: -3px;
  }

  .b_l_line {
    height: 5px;
    width: 26px;
    left: -3px;
    bottom: -3px;
  }

  .r_b_line {
    width: 5px;
    height: 24px;
    right: -3px;
    bottom: -3px;
  }

  .b_r_line {
    height: 5px;
    width: 26px;
    right: -3px;
    bottom: -3px;
  }

  .main_title {
    width: 245px;
    height: 35px;
    line-height: 33px;
    background-color: #2c58a6;
    border-radius: 18px;
    position: absolute;
    top: -17px;
    left: 45%;
    margin-left: -90px;
    color: #fff;
    font-size: 23px;
    font-weight: 530;
    box-sizing: border-box;
    padding-left: 10px;
    z-index: 1000;
    text-align: center;
  }
</style>


