<template>
    <table class="table1" :style="styleObject">
        <tr v-for="index in tableData.length">
        <td class="column">{{tableData[index-1].key}}</td>
        <td>{{tableData[index-1].value}}</td>
        </tr>
    </table>
</template>
<script>
  export default {
    data() {
      return {
        tableKey:["温度","湿度","CO2","PM2.5","能耗","人数","舒适度"],
        styleObject: {},
      // tableData:[],
        tableData: [
            { key: "温 度", value: "35.4 ℃" },
            { key: "湿 度", value: "61.4 %" },
            { key: "CO2", value: "550 ppm" },
            { key: "PM2.5", value: "60 ug" },
            { key: "能 耗", value: "34.5kwh" },
            { key: "舒适度", value: "2" },
        ]
      };
    },
    props: ['tableData', 'tableStyle', 'showByRow'],
    computed: {
      rowCount: function() {
        // return this.tableDate.length;
        return Math.ceil(this.tableKey.length/1);
      }
    },
    created() {

    },
  }
</script>



