      piePlot: {
        chart: {
          height: "350px",
          type: "pie",
          // plotBackgroundColor: null,
          // plotBorderWidth: null,
          // plotShadow: false,
          options3d: {
            enabled: true,
            alpha: 60,
            beta: 0,
          },
          backgroundColor: "none",
        },
        credits: {
          enabled: false,
        },
        colors: ["#0199ec", "#f9986b"],
        legend: {
          layout: "vertical",
          align: "right",
          itemStyle: {
            color: "#fff",
            fontSize: 12,
          },
          x: 0,
          y: 100,
          verticalAlign: "middle",
        },

        title: {
          text: null,
          style: {
            color: "white",
          },
        },

        tooltip: {
          pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>",
        },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            depth: 30,
            // showInLegend: true,
            cursor: "pointer",
            dataLabels: {
              enabled: true,
              format: "<b>{point.name}</b>: {point.percentage:.2f} %",
              style: {
                // 如果不需要数据标签阴影，可以将 textOutline 设置为 'none'
                fontSize: "20px",
                // textOutline: "1px 1px white",
              },
            },
          },
        },
        series: [
          {
            type: "pie",
            name: "人数占比",
            colorByPoint: true,
            data: [
              {
                name: "舒适度满意",
                y: 72.32,
                sliced: true,
                selected: true,
              },
              {
                name: "舒适度不满意",
                y: 27.68,
                sliced: true,
                selected: true,
              },
            ],
          },
        ],
      },
    };
  },
  mounted() {
    // this.myEcharts();]
    // document.getElementById('real-time').value = this.currentTime
    document.getElementById("real-time").value = this.timeDisplay();
    // clearInterval(this.updateTimer)
    // this._cache_ = setInterval(this.currTime, 1000);
  },
  created() {
    this.currTime = new Date();
    clearInterval(this.updateTimer);
    this.updateTimer = setInterval(() => {
      this.currTime = new Date();
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.updateTimer);
  },
  methods: {    timeDisplay() {
      // 时间展示区
      let years = this.currTime.getFullYear();
      let months = this.currTime.getMonth() + 1;
      let days = this.currTime.getDate();

      let hours =
        this.currTime.getHours() > 9
          ? this.currTime.getHours()
          : "0" + this.currTime.getHours();
      let minutes =
        this.currTime.getMinutes() > 9
          ? this.currTime.getMinutes()
          : "0" + this.currTime.getMinutes();
      let seconds =
        this.currTime.getSeconds() > 9
          ? this.currTime.getSeconds()
          : "0" + this.currTime.getSeconds();
      this.currTime =
        years +
        "/" +
        months +
        "/" +
        days +
        "  " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds;
      return (
        years +
        "/" +
        months +
        "/" +
        days +
        "  " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds
      );
    },},
  components: {},
};
</script>

